import { Component, Suspense } from "react";
import { Button, OverlayTrigger, Table, Tooltip } from "react-bootstrap";
import SearchNotFound from "../CommonComponents/NotFound/SearchNotFound";
import { TAGGSHOP_DOWNLOAD_INVOICE_PDF_URL } from "../../../../actions/sceneActions";
import saveAs from 'file-saver';
import moment from "moment";
import { FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import ViewInvoice from "./ViewInvoice";

const ActionButton = ({ toolTip, clickAction, iconImg, spinner }) => {
    return <OverlayTrigger placement="top" overlay={<Tooltip>{toolTip}</Tooltip>}>
        <Button id={`action-${iconImg}`} variant="icon" size="sm" className={`ms-2 btn-active-primary ${spinner?'spinner border':''}`} onClick={clickAction}>
            {!spinner ? <FontAwesomeIcon icon={iconImg}/> : null}
        </Button>
    </OverlayTrigger>
}

class Invoice extends Component {
    state = {
        viewInvoice: false,
        viewInvoiceData: null,
        invoiceList: [],
        downloadRequest: false
    };

    componentDidMount() {
        const { invoiceData } = this.props
        this.setState({ invoiceList: invoiceData })
    }

    viewInvoice = (data) => {
        this.setState({
            viewInvoice: true,
            viewInvoiceData: data
        })
    }

    viewInvoiceCallback = (dataEvent) => {
        this.setState({
            viewInvoice: dataEvent,
            viewInvoiceData: null
        })
    }

    clickToInvoiceDownload = (invoiceId, invoiceId2) => {
        this.setState({
            downloadRequest: invoiceId
        })
        var self = this;
        TAGGSHOP_DOWNLOAD_INVOICE_PDF_URL(invoiceId).then((response) => {
            const file = new Blob([response.data], { type: 'application/pdf' });
            const fileURL = URL.createObjectURL(file);
            saveAs(file, `Invoice-${invoiceId2}.pdf`);
            this.setState({ downloadRequest: null })
        }).catch((error) => {
            this.setState({ downloadRequest: null })
        });
    }

    downloadInvoice = (invoiceId, invoiceId2) => event => {
        this.setState({
            downloadRequest: invoiceId
        })
        var self = this;
        TAGGSHOP_DOWNLOAD_INVOICE_PDF_URL(invoiceId).then((response) => {
            const file = new Blob([response.data], { type: 'application/pdf' });
            const fileURL = URL.createObjectURL(file);
            saveAs(file, `Invoice-${invoiceId2}.pdf`);
            this.setState({ downloadRequest: null })
        }).catch((error) => {
            this.setState({ downloadRequest: null })
        });
    }

    render() {
        const { viewInvoice, invoiceList, viewInvoiceData, downloadRequest } = this.state;
        return <><div style={{ minHeight: 'calc(100vh - 600px)' }}>
            {
                invoiceList && invoiceList.length > 0 ? <Table responsive className="mb-0">
                    <thead>
                        <tr>
                            <th className="p-lg-4 p-sm-3 p-2">Date</th>
                            <th className="p-lg-4 p-sm-3 p-2">Invoice Id</th>
                            <th className="p-lg-4 p-sm-3 p-2">Plan</th>
                            <th className="p-lg-4 p-sm-3 p-2">Amount</th>
                            <th className="p-lg-4 p-sm-3 p-2">Status</th>
                            <th className="text-end p-lg-4 p-sm-3 p-2">Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            invoiceList.map((invoiceItem, invoiceIndex) => {
                                return <tr className="align-middle" key={invoiceIndex}>
                                    <td className="p-lg-4 p-sm-3 p-2 text-nowrap">{moment(invoiceItem.invoiceDate).format("DD MMM YYYY")}</td>
                                    <td className="p-lg-4 p-sm-3 p-2 text-nowrap">{invoiceItem.invoiceId}</td>
                                    <td className="p-lg-4 p-sm-3 p-2 text-nowrap">{invoiceItem.plan}</td>
                                    <td className="p-lg-4 p-sm-3 p-2 text-nowrap fw-medium">{invoiceItem.invoiceView.currencyValue}{invoiceItem.invoiceView.amount}</td>
                                    <td className="p-lg-4 p-sm-3 p-2 text-nowrap">
                                        <span className="badge badge-light-success">Paid</span>
                                    </td>
                                    <td className="p-lg-4 p-sm-3 p-2 text-nowrap text-end">
                                        <ActionButton toolTip="View" clickAction={() => this.viewInvoice(invoiceItem)} iconImg="eye" spinner={null}/>
                                        <ActionButton toolTip="Download" clickAction={() => { this.clickToInvoiceDownload(invoiceItem.invoiceId, invoiceItem.invoiceView.invoiceId) }} iconImg="download" spinner={downloadRequest==invoiceItem.invoiceId}/>
                                    </td>
                                </tr>
                            })
                        }
                    </tbody>
                </Table> : <Suspense fallback={<></>}>
                    <SearchNotFound title='No invoice was found' desc='Explore premium content! Purchase a plan and begin your journey today.' height={'calc(100vh - 600px)'} icon='product'></SearchNotFound>
                </Suspense>
            }
        </div>
            {viewInvoiceData ? <Suspense fallback={<></>}>
                <ViewInvoice viewInvoiceCallback={this.viewInvoiceCallback} modalToggle={viewInvoice} viewInvoiceData={viewInvoiceData} clickToInvoiceDownload={this.downloadInvoice} downloadRequest={downloadRequest} />
            </Suspense> : null}
        </>
    }


}

export default Invoice