import React, { Component } from 'react'
import { COMPLETE_SHOPIFY_SUBSCRIPTION, GET_PAYMENT_RESPONSE, GET_SHOPIFY_SUBSCRIPTION_LINK, PLAN__DETAILS, STRIPE_CHECKOUT, UPDATE_EC_FREE_PLAN } from '../../../../actions/planDetail';
import { TOAST_DISMISS, TOAST_LOADING, TOAST_SUCCESS } from '../../../../utils';
import { getUserInfo } from '../../../../actions/loginActions';
import withRouter from '../../../routes/WithRouter';
import { DISPATCH_SHOPIFY_CALL } from '../../../../reducer/keys';
import { connect } from 'react-redux';
import PlanView from './Elements/PlanView';
class PricePlan extends Component {
  state = { showFrePlan: false, showPlans: true, toggle: true, showContactUs: false }

  componentDidMount() {
    this.props.PLAN__DETAILS();
    const channel = new BroadcastChannel('connect');
    channel.addEventListener("message", this.readAuthentication)
    window.addEventListener("message", this.handleMessage)
  }


  readAuthentication = (event) => {
    const { dispatch, navigate } = this.props;
    const response = JSON.parse(event.data);
    const { session_id, type } = response;
    if (type == 'shopifyCallback') {
      dispatch({ type: DISPATCH_SHOPIFY_CALL, payload: session_id });
      navigate('/price/shopify/success');
    } else if (type == 'stripeCallback') {
      this.handleMessage();
    }
  };

  updatePlan = (planId) => {
    const { PLAN__DETAILS, getUserInfo, navigate } = this.props
    UPDATE_EC_FREE_PLAN(planId).then((response) => {
      const { responseCode, responseMessage } = response.data
      if (responseCode == 200) {
        TOAST_SUCCESS(responseMessage);
        getUserInfo(navigate);
        PLAN__DETAILS();
      }
    })
  }

  getPrice = (price) => (typeof price === 'string' && price.includes('$')) ? parseFloat(price.replace('$', '')) : price;

  handleShopifySubscription = (item, changePrice) => {
    var obj = { "planPrice": this.getPrice(changePrice ? item.planYearlyPrice : item.planPrice), "planName": item.planName || item.heading.toUpperCase(), "planId": item.planId || item.plan_id, "duratation": changePrice ? "ANNUAL" : "EVERY_30_DAYS" }
    GET_SHOPIFY_SUBSCRIPTION_LINK(obj).then((response) => {
      const { appSubscriptionId, confirmationUrl, status } = response.data
      if (status) {
        localStorage.setItem("appSubscriptionId", appSubscriptionId)
        window.open(confirmationUrl, "SHOPIFY", 'width=800,height=600');
      }
    }).catch((err) => {
      console.log("err", err)
    })
  }


  componentWillUnmount() {
    window.removeEventListener("message", this.handleMessage);
  }

  handleMessage = async (event) => {
    const { dispatch, navigate } = this.props;
    try {
      const response = JSON.parse(event.data);
      const { session_id, type } = response;
      if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
        if (type == 'stripeCallback') {
          if (session_id) {
            const toastId = TOAST_LOADING()
            STRIPE_CHECKOUT(session_id).then((response) => {
              const { responseCode, responseData } = response.data
              if (responseCode == 200) {
                dispatch(GET_PAYMENT_RESPONSE(responseData[0].referenceId, navigate))
              }
              else {
                TOAST_DISMISS(toastId)
                navigate('/price/failed')
              }
            }).catch(err => {
              TOAST_DISMISS(toastId)
              navigate('/price/failed')
              console.log(err)
            })
          };
        } else if (type == 'ShopifyPlanCallback') {
          if (session_id) {
            var obj = { "charge_id": session_id, "appSubscriptionId": localStorage.getItem('appSubscriptionId') }
            COMPLETE_SHOPIFY_SUBSCRIPTION(obj).then((response) => {
              const { responseData, status } = response.data 
              if (status) {
                dispatch({ type: DISPATCH_SHOPIFY_CALL, payload: responseData });
                setTimeout(() => {
                  navigate('/price/shopify/success');
                }, 100);
              }
            }).catch((err) => {
              console.log("err", err)
            })
          }; 

        }
      }
    } catch (error) {
    }
  };


  render() {
    return <PlanView {...this.props} signUpPlan={false} handleShopifySubscription={this.handleShopifySubscription} updatePlan={this.updatePlan} />
  }
}

const mapStateToProps = state => {
  return {
    authorized: state.authorized,
    priceDetail: state.planPriceDetail?.planPrice,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    PLAN__DETAILS: () => dispatch(PLAN__DETAILS()),
    getUserInfo: (history) => dispatch(getUserInfo(history))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(PricePlan));