import React, { Component } from 'react'
import { useDispatch } from 'react-redux'
import { Image, Button } from 'react-bootstrap';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom'
import { getUserInfo, getUserInfo_afterChanges } from '../../../../actions/loginActions'
import { PLAN__DETAILS, UPDATE_EC_FREE_PLAN } from '../../../../actions/planDetail'
import { togglePriceSection } from '../../../../actions/show_modal_Action'
import { TOAST_SUCCESS } from '../../../../utils'
import { HUBSPOT_URL } from '../../../../actions/api'


const PlanExpire = (props) => {
    const navigation = useNavigate()
    const dispatch = useDispatch()
    return <Plan_Expire navigation={navigation} dispatch={dispatch} />
}

class Plan_Expire extends Component {

    updatePlan = (planId) => {
        const { navigation, dispatch } = this.props
        UPDATE_EC_FREE_PLAN(planId).then((response) => {
            const { responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                TOAST_SUCCESS(responseMessage);
                dispatch(getUserInfo(navigation))
                dispatch(PLAN__DETAILS())
                navigation('/home')
            }
        })
    }

    render() {

        return (<div className="f-center h-100vh w-100 p-3">
                    <div className="w-100 text-center mb-5" style={{ maxWidth: 600 }}>
                        <div className="p-4 mb-4 position-relative d-flex align-items-center justify-content-center active">
                            <Image src={baseUrl('media/icons/color-icons/clock.svg')} height={120} width={120} alt='' fluid />
                        </div>
                        <h1>Woopsie! Your Plan Has Expired</h1>
                        <p className="fs-8 fs-medium">We have just paused your account access for now. But no need to worry, It will resume once you upgrade your plan</p>
                        <p className="fs-8 fs-medium">Are you still undecided about upgrading? Then you are welcome to extend your free trial or book a quick demo with us</p>
                        <div className='f-center my-6'>
                            <Link className="mx-3 btn btn-secondary btn-lg" target='_blank' to={`${HUBSPOT_URL}`}>Book a Demo</Link>
                            <Button size='lg' className="mx-3" onClick={() => { this.props.navigation('/price') }}>Upgrade Now!</Button>
                        </div>
                        <Button variant='link' className='text-decoration-underline' onClick={() => this.updatePlan(57)}>Switch To Forever Free Plan</Button>
                    </div>
                </div>
        )
    }
}



export default (PlanExpire);