import React, { Component } from 'react'
import { useDispatch } from 'react-redux'
import { Image } from 'react-bootstrap';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom'
import { getUserInfo } from '../../../../actions/loginActions'
import { PLAN__DETAILS, UPDATE_EC_FREE_PLAN } from '../../../../actions/planDetail'
import { TOAST_SUCCESS } from '../../../../utils'
import { TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA } from '../../../../actions/sceneActions';


const PlanFailed = (props) => {
    const navigation = useNavigate()
    const dispatch = useDispatch()
    return <Plan_Expire navigation={navigation} dispatch={dispatch} />
}

class Plan_Expire extends Component {

    updatePlan = (planId) => {
        const { navigation, dispatch } = this.props
        UPDATE_EC_FREE_PLAN(planId).then((response) => {
            const { responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                TOAST_SUCCESS(responseMessage);
                dispatch(getUserInfo(navigation))
                dispatch(PLAN__DETAILS())
                dispatch(TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA())
                navigation('/home')
            }
        })
    }

    render() {

        return (<div className="f-center h-100vh w-100 p-3">
                    <div className="w-100 text-center mb-5" style={{ maxWidth: 600 }}>
                        <div className="p-4 mb-4 position-relative d-flex align-items-center justify-content-center active">
                            <Image src={baseUrl('media/icons/color-icons/card-error.svg')} height={120} width={120} alt='' fluid />
                        </div>
                        <h1>Your payment failed</h1>
                        {/* <p className="fs-8 fs-medium">Please try again</p> */}
                        <div className='f-center my-6'>
                            <Link className="mx-3 btn btn-secondary btn-lg" target='_blank' to={'/home'}>Back to Dashboard</Link>
                        </div>
                    </div>
                </div>
        )
    }
}



export default (PlanFailed);