import React from "react";
import { connect } from "react-redux";
import { Button, Accordion, Card, Container } from "react-bootstrap";
import { baseUrl, FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import withRouter from "../../../routes/WithRouter";
import { TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA } from "../../../../actions/sceneActions";
import Alternative from "./Component/Alternative";
import Expensive from "./Component/Expensive";
import TechnicalIssue from "./Component/TechnicalIssue";
import HardToUse from "./Component/HardToUse";
import Other from "./Component/Other";
import './cancel-plan.scss'
import { CANCEL_SUBSCRIPTION, PLAN_DISCOUNT } from "../../../../actions/api";
import { TOAST_ERROR, TOAST_SUCCESS } from "../../../../utils";
import HttpClient from "../../../../actions/http-client";
import CancelPage from "./Component/CancelPage";
const reasons = [
    {
        title: 'Found a better alternative', 
        desc: 'Did you find a product with better features or price than ours?',
        icon: 'right-left',
        RenderElement:Alternative
    },
    {
        title: 'Too expensive', 
        desc: 'Did the price we offer not match your expectations?', 
        icon: 'circle-dollar',
        RenderElement:Expensive
    },
    {
        title: 'Technical issues', 
        desc: 'Finding it hard to avoid technical glitches?', 
        icon: 'headset',
        RenderElement:TechnicalIssue
    },
    {
        title: 'Hard to setup and use', 
        desc: 'Do you need any help with setting up the Gallery to get the most out of it?', 
        icon: 'circle-question',
        RenderElement:HardToUse
    },
    {
        title: 'Others (please specify)', 
        desc: "Is there anything else that's bothering you?", 
        icon: 'circle-ellipsis',
        RenderElement:Other
    }
]

class Cancellation extends React.Component {

    constructor(props) {
        super(props);
        this.state ={ 
            pageData: {}, 
            pageStatus: 0, 
            acceptOfferData: {},
            cancellationType: null,
            productUpdate:false,
            selectedReason:-1,
            activeKey: null,
            feedBackName:'',
            contentError: false,
            otherValue:'',
            otherError:false,
            contentValue:'',
            loader:false,
            offerRequest:false,
            cancelType:null
        };
    }

    componentWillMount(){
        const {productData} = this.props
        if(!(productData && Object.keys(productData).length > 0)) this.props.TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA();
    }

    setFeedBack = (name) => {this.setState({feedBackName: name})}

    setContent = (val) => {this.setState({contentValue: val, contentError:false})}

    setOtherValue = (val) => {this.setState({otherValue:val, otherError:false})}

    acceptOfferSubscription = (value) => { this.setState({ acceptOfferData: value }) }

    cancelApi = () => {
        const { productData } = this.props
        const { selectedReason, feedBackName, contentValue, otherValue } = this.state
        let error = false
        const obj = {
            planId: productData.userProduct.planId,
            type: selectedReason+1,
            name: reasons[selectedReason].title,
            feedBackName: '',
            feedbackContent: '',
            feedbackOption: '',
        }

        if (selectedReason == 0) {
            if(String(feedBackName).includes('Prefer')){
                if(otherValue.trim().length <= 0){
                    this.setState({otherError:true})
                    error = true
                }
                else obj['feedBackName'] = otherValue;
            }
        }

        else if(selectedReason == 2){
            obj['feedbackOption'] = feedBackName;
        }

        if(contentValue.trim().length <= 0) {
            if((selectedReason == 2 && feedBackName.includes('Anything')) || selectedReason!=2){
                error = true
                this.setState({contentError : true})
            }
        }
        else obj['feedbackContent'] = contentValue;
        if(!error){
            this.setState({loader:true})
            new HttpClient().post(CANCEL_SUBSCRIPTION, obj).then(res => {
                this.setState({loader:false});
                if (res && Object.keys(res).length > 0) {
                    const { responseMessage } = res.data;
                    if (res.status == 200) {
                        TOAST_SUCCESS(responseMessage)
                        this.props.TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA();
                        if (selectedReason == 0 || selectedReason == 2 || selectedReason == 4) {
                            this.setState({cancelType:1});
                        } else {
                            this.setState({cancelType:2});
                        }
                    } else {
                        TOAST_ERROR(responseMessage)
                    }
                }
            }).catch(ex => {
                if (ex && Object.keys(ex).length > 0) {
                    const { data } = Object.keys(ex).length > 0 ? ex.response : {};
                    const { responseMessage } = data;
                    const msg = (typeof responseMessage) == 'object' ? "Subscription not found" : responseMessage;
                    if (data && Object.keys(data).length > 0) {
                        TOAST_ERROR(msg)
                    } else TOAST_ERROR('Something went worng!');
    
                }
                this.setState({loader:false});
            })
        }
    }

    resetData = () => {
        this.setState({selectedReason:-1, activeKey:null, contentError:false, otherError:false, otherValue:'', contentValue:'', feedBackName:''})
    }

    acceptOffer = () => {
        const { productData } = this.props
        this.setState({offerRequest:true})
        new HttpClient().post(PLAN_DISCOUNT, {
            planId: productData.userProduct.planId,
            productId: productData.Product.id,
        }).then(res => {
            if (Object.keys(res).length > 0) {
                const { responseMessage, responseCode, responseData } = res.data;
                this.setState({offerRequest:false});
                if (responseCode == 200) {
                    TOAST_SUCCESS(responseMessage);
                    this.setState({offerData: responseData})
                    this.setState({cancelType:3});
                }
                else {
                    TOAST_ERROR(responseMessage);
                }
            }
        }).catch(ex => {
            if (ex && Object.keys(ex).length > 0) {
                const { data } = Object.keys(ex).length > 0 ? ex.response : {};
                const { responseMessage } = data;
                const msg = (typeof responseMessage) == 'object' ? "Subscription not found" : responseMessage;
                if (data && Object.keys(data).length > 0) {
                    TOAST_ERROR(msg);
                } else TOAST_ERROR('Something went worng, please try again later.');
            }
            this.setState({offerRequest:false});

        })
    }

    
    render() {
        const { selectedReason, activeKey, feedBackName, contentError, otherError, contentValue, otherValue, loader, offerRequest, offerData, cancelType } = this.state;
        const { navigate, productData } = this.props;

        let planId = 0;
        if(productData && Object.keys(productData).length > 0) {
            planId = productData.userProduct.planId
        }

        return <div className="d-flex flex-column flex-column-fluid w-100 py-5 cancel_reasons">
                    <Container fluid>
                        <Card>
                            <Card.Body>
                                {!cancelType ? <div className="mx-auto w-100" style={{maxWidth:600}}>
                                    <div style={{minHeight:'calc(100vh - 220px)'}}>
                                        <div className="mx-auto f-center mt-10 mb-6" style={{maxWidth:450}}>
                                            <h3 className="h4 fw-bold mb-0 text-center">Please let us know the reason you want to cancel your Tagshop Subscription.</h3>
                                        </div>
                                        <Accordion className="arrow_disabled" flush onSelect={e=>this.setState({selectedReason: e!=null ? e : selectedReason, activeKey:e!=null ? e : activeKey})} activeKey={activeKey}>
                                        {reasons.map((item, index) => { 
                                            return !((planId == 53 || planId == 58) && item.title == 'Too expensive') ? 
                                            selectedReason == index || selectedReason == -1 ?
                                            <Accordion.Item className="border-0 mb-3" eventKey={index} >
                                                <Accordion.Header className="box-shadow rounded-2 mb-3 bg-light">
                                                    <div className="symbol symbol-40 flex-shrink-0 me-3 text-primary">
                                                        <FontAwesomeIcon  icon={item.icon} iconClass={'fs-2'}/>
                                                    </div>
                                                    <div className="overflow-hidden text-start">
                                                        <h6 className="fs-8 mb-1">{item.title}</h6>
                                                        <p className="fs-8 mb-0 text-gray-600 block-center justify-content-start">{item.desc}</p>
                                                    </div>
                                                </Accordion.Header>
                                                <Accordion.Body className="bg-body rounded-2">
                                                    {selectedReason == activeKey ? <item.RenderElement reason={''} setFeedBack={this.setFeedBack} feedBackName={feedBackName} contentError={contentError} otherError={otherError} contentValue={contentValue} setContent={this.setContent} otherValue={otherValue} setOtherValue={this.setOtherValue} offerRequest={offerRequest} acceptOffer={this.acceptOffer}/> : null}
                                                </Accordion.Body>
                                            </Accordion.Item>
                                            : null : null})}
                                        </Accordion>
                                    </div>
                                    <div className="d-flex justify-content-end mb-5">
                                        <Button onClick={()=>selectedReason == -1 ? navigate('/transactions') : this.resetData()} className="btn btn-secondary" disabled={loader || offerRequest}>{selectedReason == -1 ? 'Cancel' : 'I’ve changed my mind, Go Back'}</Button>

                                        {selectedReason == -1 ?<Button onClick = {()=> navigate('/transactions')} className="btn btn-primary ms-3">Keep My Subscription</Button> : null}

                                        {selectedReason!= -1 && selectedReason!= 1 && selectedReason!= 3  ? <Button variant={"danger"} className={`ms-3 ${loader ? 'spinner' : ''}`} onClick={()=>this.cancelApi()}>Continue to Cancel</Button> : null}
                                    </div>
                                </div> :
                                <div className="mx-auto w-100" style={{maxWidth:600}}>
                                    <div className="f-center flex-column" style={{minHeight:'calc(100vh - 130px)'}}>
                                        <CancelPage cancelType={cancelType} offerData={offerData}/>
                                    </div>
                                </div> }
                            </Card.Body>
                        </Card>
                    </Container>
            </div>
    }
}

const mapStateToProps = (state) => {
    return {
        cancellationDetails: state.userAccountData,
        productData: state.userAccountData?.productSubscriptions,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA: () => dispatch(TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA()),
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(Cancellation));