import { Component, Suspense } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { SHOPIFY_CANCEL_SUBSCRIPTION, TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA } from "../../../../actions/sceneActions";
import PlanDetails from "./PlanDetails";
import { Loader } from "../CommonComponents/Loader";
import PlanLimit from "./PlanLimit";
import { Row, Container, Button, Card } from "react-bootstrap";
import Invoice from "./Invoice";
import Swal from "sweetalert2";
import { FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import { showUpgrade } from "../../../../actions/collaboratorActions";
import withRouter from "../../../routes/WithRouter";

class AccountInfo extends Component {

    state={
        showInvoice:false,
    }

    componentDidMount() {
        const { productData } = this.props
        if(!(productData && Object.keys(productData).length > 0)) this.props.TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA();
    }

    toggleInvoice = () => this.setState({showInvoice : !this.state.showInvoice})

    cancelShopify = (planId) => {
        Swal.fire({
            title: "Confirm?",
            text: 'Do you want to cancel your subscription with Tagshop?',
            icon: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            cancelButtonText: "No",
        }).then((result) => {
            if (result.value) {
                const data = { 'planId': planId }
                SHOPIFY_CANCEL_SUBSCRIPTION(data).then((res) => {
                    const { status } = res.data
                    this.props.TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA();
                });
            }
        });
    }

    render(){
        const { productData, navigate, userData } = this.props
        const {showInvoice} = this.state
        return <div className='d-flex flex-column flex-column-fluid full-content'>
                    <div className="subheader">
                        <Container fluid>
                            <div className="nav nav-underline">
                                <div className='nav-item'>
                                    <Link to={'/profile'} className="nav-link">Profile</Link>
                                </div>
                                <div className='nav-item'>
                                    <Link to={'/transactions'} className="nav-link active">Plan Details</Link>
                                </div>
                                <div className='nav-item'>
                                    <Link to={'/social-account'} className="nav-link">Social Accounts</Link>
                                </div>
                                <div className='nav-item'>
                                    <button onClick={()=> userData.collaboratorAccess ? navigate('/collaborator') : showUpgrade(navigate)} className="nav-link">Collaborator</button> 
                                </div>
                            </div>
                        </Container>
                    </div>
                    <Container>
                        {productData && Object.keys(productData).length > 0 ? <><Suspense fallback={<></>}>
                            <PlanDetails productData={productData} showInvoice={showInvoice} toggleInvoice={this.toggleInvoice} cancelMethod={!(productData.userProduct.cancelSubscription || productData.userProduct.is_trial || productData.userProduct.planId === '57') ? productData.userProduct.source == 1 ? 1 : productData.userProduct.source == 2 ? 2 : 0 : 0} cancelShopify={this.cancelShopify}/>
                        </Suspense>
                        {showInvoice ? <Button variant='link' className="text-muted mb-3" onClick={this.toggleInvoice}>
                            <FontAwesomeIcon icon={'arrow-left'} iconClass={'me-1'}/>
                            Back to Feature
                        </Button>:null}
                        <Card className="mb-xl-5 mb-4">
                            <Card.Body className='p-xl-5' style={{filter: productData.userProduct.cancelSubscription == 1 && !showInvoice ? 'blur(6px)' : ''}}>
                                <h3 className='fs-6 mb-5'>{showInvoice ? "Invoice" : "Feature Usage"}</h3>
                                <Suspense fallback={<></>}>
                                    {showInvoice ? 
                                        <Invoice invoiceData={productData && Object.keys(productData).length > 0 ? productData.invoices : []}/>
                                    : 
                                    <Row className="gx-4">
                                        { productData && productData.featuresLimit?.length >0 ? productData.featuresLimit.map((data, index)=>{
                                            if(data.total) return <PlanLimit title={data.title} max={data.total} min={data.using}></PlanLimit>
                                        })
                                        : null }
                                    </Row>}
                                </Suspense>
                            </Card.Body>
                            {productData.userProduct.cancelSubscription == 1 && !showInvoice ? <div className="f-center h-100 position-absolute top-0 start-0 w-100" style={{backgroundColor:'rgba(var(--bs-body-rgb), .5)',}}>
                                <div className="w-100 text-center">
                                    <p className="text-danger h4 mb-5">Your plan has been cancelled.</p>
                                    <Link to={'/price'} className="btn btn-danger">Upgrade Now</Link>
                                </div>
                            </div> : null}
                        </Card></> : 
                        <div style={{minHeight:'calc(100vh - 180px)'}}>
                        <Loader/>
                        </div>
                        }
                    </Container>
                </div>
        
    }
}

const mapStateToProps = state => {
    return {
        productData: state.userAccountData?.productSubscriptions,
        userData: state.authorized?.userData?.user_data,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA: () => dispatch(TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA()),
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(AccountInfo))