import React, { Component } from 'react';
import {ProgressBar, Button, Col, Card, Badge} from 'react-bootstrap'; 
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import { OPEN_INTERCOM } from '../../../../utils';
class PlanLimit extends Component{
    render(){
        const {key, title, min , max} = this.props
        let progress = parseInt((min / (max ? max : 100)) * 100);
        return <Col md={6} key={key}>
            <Card className={`shadow-none border`}>
                <Card.Body>
                    <div className='d-flex justify-content-between mb-2'>
                        <p className='mb-2 fw-medium p-1 text-muted'>
                            {`${`${max} ${title}`}`}
                            <Badge className={`badge-${progress >= 70 ? 'danger' : progress >=50 ? 'warning' : 'primary'} ms-2`} bg="">{min} Used</Badge>
                        </p>
                        <div>
                        {progress >= 50 ? <Button variant='outline-primary' size={'sm'} className='ms-3' onClick={OPEN_INTERCOM}>
                            <FontAwesomeIcon icon={'comment'} iconClass={'me-1'}/>
                            Talk to Sales
                            </Button> : null}
                        </div>
                    </div>
                    <ProgressBar now={progress} variant={progress >= 70 ? 'danger' : progress >=50 ? 'warning' : 'primary'} className='h-12px'/>
                </Card.Body>
            </Card>
        </Col>
    }
}

export default PlanLimit