import { useEffect, useState } from "react";
import { Button, Image } from "react-bootstrap"
import { useNavigate } from "react-router-dom";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";

const dataJson = [
    {
        type: 1,
        heading: "Thank you for letting us know.",
        text: 'Your cancellation has been processed successfully. If you have any further questions or concerns, please feel free to reach out.',
        icon: 'check'
    },
    {
        type: 2,
        heading: "We're sorry to see you go.",
        text: 'Your cancellation has been processed successfully. If you have any further questions or concerns, please feel free to reach out.',
        icon: ''
    },
    {
        type: 3,
        heading: "Your effective price will be ",
        text: 'Great! Your discount is now active. You will be charged 10% less on your next billing date.',
        icon: 'badge-percent'
    },
]

const CancelPage = ({cancelType, offerData}) => {
    const [pageData, setPageData] = useState();
    const navigate = useNavigate()

    useEffect(() => {
        const res = dataJson.filter((value) => value.type == cancelType)
        setPageData(res.length > 0 ? res[0] : res)
    }, [cancelType]);

    return( pageData && Object.keys(pageData).length > 0 ? <> 
        <div className="text-center py-4">
            <span className="h-80px w-80px text-gray-100 bg-success rounded-circle mx-auto f-center mb-5 display-1">
                <FontAwesomeIcon icon={pageData.icon}/>
            </span>
            <h3 className="mb-4 h2">{pageData.heading} {cancelType == 3 ? <>
                <del className='text-gray-400'>
                    {offerData.currency == 'inr' ? '₹' : '$'}
                    {offerData.beforePercentage}
                    </del>
                <span className='text-danger'>
                    {offerData.currency == 'inr' ? '₹' : '$'}{offerData.afterPercentage}/{offerData.interval}
                </span>
            </> : null}</h3>
            <p className="fw-medium mb-5 text-muted">{pageData.text}</p>
            <Button variant="primary" size="lg" onClick={()=>navigate('/home')}>Go To Home</Button>
        </div>
    </> : null)
}

export default CancelPage