import moment from "moment";
import { Component } from "react";
import { Button, Row, Col, OverlayTrigger, Tooltip } from 'react-bootstrap';
import withRouter from "../../../routes/WithRouter";
import { FontAwesomeIcon } from "../../../../Assets/AssetsHelper";


const CurrentOverView = ({ heading, value, tooltip, tooltip2, btnIcon, history, toggleInvoice, icon, viewInvoice, method, secondMethod }) => {
    return <Col xl={3} sm={6}>
        <div className="py-4 px-3 d-flex align-items-center border box-shadow rounded-2 mb-3">
            <div className='d-flex flex-grow-1 align-items-center pe-2'>
                <div className="symbol symbol-48 flex-shrink-0 me-3 text-primary bg-gray-200">
                    <FontAwesomeIcon icon={icon} iconClass={'fs-1'} />
                </div>
                <div className="overflow-hidden text-start">
                    <h6 className="h4 mb-1 text-ellipsis">{heading}</h6>
                    <p className="fs-8 mb-0 text-gray-600 text-ellipsis block-center justify-content-start lh-sm">{value}
                        {viewInvoice ? <OverlayTrigger placement="top" overlay={<Tooltip>Download last Invoice</Tooltip>}>
                            <Button type="button" className="btn btn-icon h-auto w-auto p-0 border-0 ms-2" onClick={() => viewInvoice()}><i className="ri-download-2-line icon-5"></i></Button>
                        </OverlayTrigger> : null}
                    </p>
                </div>
            </div>
            <div className="f-center">
                {tooltip ? <OverlayTrigger placement="top" overlay={<Tooltip>{tooltip}</Tooltip>}>
                    <Button variant='icon' size="sm" className='btn-active-primary' onClick={method}>
                        <FontAwesomeIcon icon={btnIcon} />
                    </Button></OverlayTrigger> : null}
                {secondMethod ?
                    <OverlayTrigger placement="top" overlay={<Tooltip>{tooltip2}</Tooltip>}>
                        <Button variant='icon' size="sm" className='btn-active-danger ms-2' onClick={secondMethod}>
                            <FontAwesomeIcon icon={'trash-can'} />
                        </Button></OverlayTrigger> : null}
            </div>
        </div>
    </Col>
}

class PlanDetails extends Component {
    render() {
        const { productData, toggleInvoice, cancelMethod, cancelShopify, navigate } = this.props
        return <Row className="mb-5 gx-3">
            <CurrentOverView heading="Plan Name" tooltip="Change Plan" tooltip2="Remove Subscriptions" btnIcon="pen-to-square" value={`${productData.userProduct.name} - Tagshop`} method={() => navigate('/price')} secondMethod={cancelMethod ? cancelMethod == 1 ? () => { navigate('/transactions/cancel') } : () => cancelShopify(productData.userProduct.planId) : null} icon="star" />
            <CurrentOverView heading="Pricing" value={`${productData.Product.currency}${productData.userProduct.price}`} icon="circle-dollar" />
            <CurrentOverView heading="Expires At" value={productData.userProduct.cancelSubscriptionDate > 0 ? moment.unix(productData.userProduct.cancelSubscriptionDate).format('Do MMM YYYY') : productData.userProduct.expiresAt > 0 ? moment.unix(productData.userProduct.expiresAt).format('Do MMM YYYY') : `--`} icon="credit-card" />
            <CurrentOverView heading="Invoice" value={productData.invoices.length > 0 ? moment(productData.invoices[0].invoiceDate).format('DD MMM YYYY') : `--`} tooltip={'Show All Invoices'} method={() => { toggleInvoice() }} icon="file-invoice-dollar" btnIcon={'eye'} />
        </Row>
    }
}

export default withRouter(PlanDetails)