import React from 'react';
import { Form } from "react-bootstrap"

const Other = ({setFeedBack, contentError, contentValue, setContent}) => {
    return  <div>
            <p className="fw-medium mb-4">Please let us know the issues so that we can try and resolve it to our best capabilities.</p>
            <Form.Control as='textarea' rows={4} placeholder="Enter your feedback" className={`${contentError ? 'is-invalid' : ''}`} value={contentValue} onChange={(e)=>setContent(e.target.value)}></Form.Control>
            {contentError ? <span className='invalid-feedback'>This Field is Required.</span> : null}
        </div>

}

export default Other;
