import React, { useEffect, useState } from 'react';
import { Form } from "react-bootstrap"

const alternative = [
    "Flowbox",
    "Foursixty",
    "Cevoid",
    "Covet Pics",
    "Add Sauce",
    "Prefer something else? Let us know your enchanting choice below!"
]
const Alternative = ({setFeedBack, contentError, otherError, contentValue, setContent, otherValue, setOtherValue}) => {
    const [altType, setAltType] = useState()
    
    useEffect(() => {
        setAltType(0)
        setFeedBack(alternative[0])
    },[])

    return  <div>
        <p className="fw-medium mb-4">We appreciate your feedback. May we ask which alternative you found more suitable? Your insights helps us improve!</p>
        <div className="mb-3">
            {alternative.map((item, index) => (
            <Form.Check key={index} inline label={item} className="mb-3" name="alternative" type="radio" id={`inline-${index}`} checked={index == altType ? true : false} onClick={()=>{setAltType(index); setFeedBack(item)}}/>
            ))}
        </div>

        {altType == 5 ? <Form.Control as='input' className={`${otherError ? 'is-invalid' : 'mb-2'}`} placeholder="Enter Alternative Name" value={otherValue} onChange={(e)=> setOtherValue(e.target.value)}></Form.Control> : null}

        {otherError ? <span className='invalid-feedback mb-2'>This Field is Required.</span> : null}

        <Form.Control as='textarea' className={`${contentError ? 'is-invalid' : ''}`} rows={4} placeholder="Enter your feedback" value={contentValue} onChange={(e)=>setContent(e.target.value)}></Form.Control>

        {contentError ? <span className='invalid-feedback'>This Field is Required.</span> : null}
    </div>
}

export default Alternative;
