import React, { useState, useEffect } from 'react';
import Modal from 'react-bootstrap/Modal';
import { useSelector } from 'react-redux';
import { HUBSPOT_URL } from '../../actions/api';
import { Button, Image } from 'react-bootstrap';
import { baseUrl } from '../../Assets/AssetsHelper';
import { useNavigate } from 'react-router-dom';
const message = (access, assisted) => {
    const upgrd100Txt = `Upgrade now to ensure your content remains live and continues driving engagement and business opportunities.`
    const upgrd90Txt = `Upgrade to a higher plan now to continue showcasing your engaging content seamlessly and driving results on your website.`
    if (access == 1 && assisted == 1) return { heading: "Your gallery view and assisted revenue limit has been reached!", text: `You’ve hit the maximum gallery view limit and assisted revenue limit for your plan. ${upgrd100Txt}`, isSkip: false }
    else if (access == 2 && assisted == 2) return { heading: "Your gallery view and assisted revenue is nearing its limit!", text: `You’ve used 90% of your gallery view quota and hit the maximum assisted revenue limit for your plan. ${upgrd90Txt}`, isSkip: true }
    else if (access == 1 || assisted == 1) return { heading: `Your ${access == 1 ? `gallery view` : assisted == 1 ? `assisted revenue` : ``} limit has been reached!`, text: `You’ve hit the maximum  ${access == 1 ? `gallery view` : assisted == 1 ? `assisted revenue` : ``} limit for your plan. ${upgrd100Txt}`, isSkip: false }
    else if (access == 2 || assisted == 2) return { heading: `Your ${access == 2 ? `gallery view` : assisted == 2 ? `assisted revenue` : ``} is nearing its limit!`, text: `You’ve used 90% of your ${access == 2 ? `gallery view quota` : assisted == 2 ? `assisted revenue limit` : ``} . ${upgrd90Txt}`, isSkip: true }
}
function PlanLimitExceed() {
    const [show, setShow] = useState(false);
    const navigate = useNavigate()
    const { userData } = useSelector(state => state.authorized)
    const restriction_exceed = userData?.user_data?.user?.restriction_exceed
    const assisted_exceed = userData?.user_data?.user?.revenue_exceed
    useEffect(() => {
        const timer = setTimeout(() => {
            setShow(true);
        }, 100);
        return () => clearTimeout(timer);
    }, []);

    const handleClose = () => {
        setShow(false)
    }
    const { heading, text, isSkip } = message(restriction_exceed, assisted_exceed)
    return (
        <Modal show={show} backdrop="static" keyboard={false} size={'lg'} aria-labelledby="planLimit" centered>
            <Modal.Body>
                {JSON.parse(localStorage.getItem('isAdmin')) ?
                    <Button variant='close' className="position-absolute end-0 top-0 m-5" onClick={handleClose}></Button>
                    : <></>}
                <div className="p-lg-10 p-sm-8 mt-5 text-center">
                    <Image className='mb-6' src={baseUrl('media/images/error/warning.svg')} width="65" height="65" fluid />
                    <h2 className="h3">{heading}</h2>
                    <p className="mb-6 fs-8 text-muted fw-medium">{text}</p>
                    <div className={`text-center ${isSkip ? `mb-4` : ``}`}>
                        <button type="button" className="btn btn-primary me-4" onClick={() => { navigate('/price'); document.body.classList.add('dashUpgradeModal_opened') }}>Upgrade Now</button>
                        <a type="button" href={HUBSPOT_URL} target='_blank' rel='noreferrer' className="btn btn-outline-dark me-4 btn-min-w">Book a call</a>
                    </div>
                    {
                        isSkip ? <div className='f-center'>
                            <Button variant='link' onClick={handleClose}>Skip</Button>
                        </div> : <></>
                    }

                </div>
            </Modal.Body>
        </Modal>
    );
}
export default PlanLimitExceed;