import React, { Component, Suspense } from 'react';
import { connect } from "react-redux";
import { Link } from 'react-router-dom';
import { Container, Card, Button, Form, Row, Col } from 'react-bootstrap';
import ViewProfile from './ViewProfile';
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import EditProfile from './EditProfile';
import { containsSpecialChars, TOAST_ERROR, TOAST_SUCCESS, validatePassword } from '../../../../utils';
import HttpClient from '../../../../actions/http-client';
import { PROFILE_UPDATE } from '../../../../actions/api';
import { getUserInfo_afterChanges } from '../../../../actions/loginActions';
import PwdStrength from '../CommonComponents/PwdStrength';
import withRouter from '../../../routes/WithRouter';
import { checkPermission, showUpgrade } from '../../../../actions/collaboratorActions';

class Profile extends Component{
    state = {
        isRequesting: false,
        resetPasswordToggle: false,
        password: '',
        confirmPassword: '',
        resetPasswordError: false,
        visiblePass: false,
        visibleConfirmPass: false,
        isEditProfile: false,
        passwordMatchError: false,
        strength:0,
        strongPass:true
    };

    resetPasswordToggle = () => {
        this.setState({
            resetPasswordToggle: !this.state.resetPasswordToggle,
            resetPasswordError: false,
            passwordMatchError: false,
            password:'',
            confirmPassword:'',
            strength:0
        })
    }

    onChangePassword = event => {
        const {strength} =  validatePassword(event.target.value)
        this.setState({ password: event.target.value, resetPasswordError: event.target.value.length < 8, strength: strength , strongPass: strength==4 ? true :this.state.strongPass})
    }

    onChangeConfirmPassword = event => {
        this.setState({ confirmPassword: event.target.value }, () => {
            if (this.state.password != this.state.confirmPassword) this.setState({ passwordMatchError: true })
            else this.setState({ passwordMatchError: false })
        })
    }

    onClickResetPassword = event => {

        const { userData, history } = this.props
        const { password, resetPasswordError, passwordMatchError, confirmPassword, strength } = this.state
        if(strength == 4){
        if (password && String(password).trim().length > 8 && !resetPasswordError && !passwordMatchError && String(confirmPassword)==String(password)) {
            this.setState({isRequesting: true})
            const fullName = userData.user.name.split(" ")
            const data = {
                "organization": userData.user.organization,
                "industry": userData.user.industry,
                "picture": userData.user.picture,
                "firstName": fullName[0],
                "lastName": fullName[1] ? fullName[1] : "",
                password: password
            }

            new HttpClient().post(PROFILE_UPDATE, data).then((response) => {
                const { response_message } = response.data;
                TOAST_SUCCESS('Password Updated Successfully');
                this.props.getUserInfo()
                this.setState({resetPasswordToggle:false, isRequesting: false})

            }).catch(ex => {
                TOAST_ERROR('Something went wrong, please try again later.');
                this.setState({resetPasswordToggle:false, isRequesting: false})
            })
        }
        else {
            String(confirmPassword) != String(password) ? this.setState({ passwordMatchError: true }) : this.setState({resetPasswordError:true})
        }
    }
    else{
          this.setState({strongPass:false})
      }
    }

    showPassword = (type) => event => {
        const { visiblePass, visibleConfirmPass } = this.state
        if (type == 'confirm') this.setState({ visibleConfirmPass: !visibleConfirmPass })
        else this.setState({ visiblePass: !visiblePass })
    }

    editProileCallback = event => {
        this.setState({
            isEditProfile: event
        })
    }

    render(){
        const { resetPasswordToggle, isRequesting, visiblePass, visibleConfirmPass, resetPasswordError, isEditProfile, passwordMatchError, strength, password, strongPass } = this.state;
        const {userData, navigate} = this.props
        return <div className='d-flex flex-column flex-column-fluid full-content'>
                    <div className="subheader">
                        <Container fluid>
                            <div className="nav nav-underline">
                                <div className='nav-item'>
                                    <Link to={'/profile'} className="nav-link active">Profile</Link>
                                </div>
                                {userData && (userData.collaborator == '' || userData.collaborator?.activeAccount == 0) ? <div className='nav-item'>
                                    <Link to={'/transactions'} className="nav-link">Plan Details</Link>
                                </div> : null}
                                <div className='nav-item'>
                                    <button onClick={()=>checkPermission('networkManager', 'manageAccount') ? navigate('/social-account') : {}} className="nav-link">Social Accounts</button>
                                </div>
                                {userData && (userData.collaborator == '' || userData.collaborator?.activeAccount == 0 || userData.collaborator?.permissions == '') ? <div className='nav-item'>
                                    <button onClick={()=> userData.collaboratorAccess ? navigate('/collaborator') : showUpgrade(navigate)} className="nav-link">Collaborator</button> 
                                </div> : null}
                            </div>
                        </Container>
                    </div>
                    <Container>
                        <Card className="mb-xl-5 mb-4">
                            <Card.Body className='p-xl-5' style={{minHeight:300}}>
                            {!isEditProfile ? 
                            <Suspense fallback={<></>}>
                                <ViewProfile isEditProfile={isEditProfile} editProileCallback={this.editProileCallback} loading={userData && Object.keys(userData).length > 0 ? true : false} />
                            </Suspense>
                            :
                            <Suspense fallback={<></>}>
                                <EditProfile isEditProfile={isEditProfile} editProileCallback={this.editProileCallback} />
                            </Suspense>
                            }
                            </Card.Body>
                        </Card>
                        <Card className="mb-xl-5 mb-4">
                            <Card.Body className='p-xl-5'>
                                <h3 className="fs-6 mb-5">Reset Password</h3>
                                {!resetPasswordToggle ?
                                    <div className="mb-2 mb-xl-3 d-flex align-items-center justify-content-between flex-wrap">
                                        <div className="d-flex flex-column flew-grow-1">
                                            <label className="fw-semibold text-muted mb-2">Password</label>
                                            <span className="fw-bold fs-8">**************</span>
                                        </div>
                                        <div className="d-flex mt-4">
                                            <Button variant='secondary' className={`me-2 ${userData && Object.keys(userData).length > 0 ? '':'disabled'}`} onClick={this.resetPasswordToggle}>Reset Password</Button>
                                        </div>
                                    </div>
                                    :
                                    <Row className="mb-2 mb-xl-3">
                                        <Col xl={4} lg={6} className="mb-3 mb-lg-0">
                                            <Form.Label>New Password</Form.Label>
                                            <div className={`position-relative mb-2 ${resetPasswordError ? `is-invalid` : ``}`}>
                                                <Form.Control type={visiblePass ? "text" : "password"} name="password" size='lg' className={`mb-1`} placeholder="Please enter password.." onChange={this.onChangePassword} autoComplete='off' />
                                                <span className="view_pass fs-6 text-gray-500 position-absolute translate-middle top-50 cursor-pointer h-34px w-34px f-center" onClick={this.showPassword('')}>
                                                    <FontAwesomeIcon icon={visiblePass ? 'eye' : 'eye-slash'}/>
                                                </span>
                                            </div>
                                            {resetPasswordError ? <span className='invalid-feedback mb-1'>{password && password.length < 8 ? 'Enter valid password' : 'Password is required.'}</span> : null}
                                            <div className={`${strongPass ? 'text-muted' :'text-danger'} mb-5`}>Password must be at least 8 characters long and include a combination of letters, numbers, and special symbols (e.g., @, #, $, %).</div>
                                            <PwdStrength strength={strength}/>
                                        </Col>
                                        <Col xl={4} lg={6} className="mb-3 mb-sm-0">
                                            <Form.Label>Confirm New Password</Form.Label>
                                            <div className={`position-relative ${passwordMatchError ? `is-invalid mb-1` : ``}`}>
                                                <Form.Control type={visibleConfirmPass ? "text" : "password"} name="cpassword" size='lg' className={`${passwordMatchError ? `is-invalid` : ``}`} placeholder="Please enter confirm password.." onChange={this.onChangeConfirmPassword} autoComplete='off' />

                                                <span className="view_pass fs-6 text-gray-500 position-absolute translate-middle top-50 cursor-pointer h-34px w-34px f-center" onClick={this.showPassword("confirm")}>
                                                    <FontAwesomeIcon icon={visibleConfirmPass ? 'eye' : 'eye-slash'}/>
                                                </span>
                                            </div>
                                            {passwordMatchError ? <div className="invalid-feedback">Password does not match.</div> : null}
                                        </Col>
                                        <Col sm={12} className="mt-2">
                                            <div className="d-flex align-items-center justify-content-end flex-wrap mt-3 mt-lg-0">
                                                <Button variant='secondary' className=" me-3" onClick={this.resetPasswordToggle}>Cancel</Button>
                                                <Button className={isRequesting ? `spinner` : ''} onClick={this.onClickResetPassword}>Update Password</Button>
                                            </div>
                                        </Col>
                                    </Row>}
                            </Card.Body>
                        </Card>
                    </Container> 
                </div>
    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authorized?.userData?.user_data,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getUserInfo: () => dispatch(getUserInfo_afterChanges())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter (Profile))