import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { HUBSPOT_URL } from "../../../../../actions/api";
import { Button, Form, Alert } from "react-bootstrap"
import { OPEN_INTERCOM } from "../../../../../utils";

const issues = [
    "Didn't find the features useful?",
    "Got concerns regarding security and compliance.",
    "Didn't find the UI attractive.",
    "Frequent bugs and errors.",
    "Anything else."
]
const TechnicalIssue = ({setFeedBack, contentError, contentValue, setContent}) => {
    
    const [altType, setAltType] = useState()
    
    useEffect(() => {
        setAltType(0)
        setFeedBack(issues[0])
    },[])

    return <div>
            <p className="fw-medium mb-4">Could you specify the issue you are experiencing with Tagshop?</p>
            <div className="mb-4">
                {issues.map((item, index) => (
                <Form.Check key={index} inline label={item} className="mb-3" name="issue" type="radio" id={`inline-${index}`} checked={index == altType ? true : false} onClick={()=>{setAltType(index); setFeedBack(item)}}/>
                ))}
            </div>

            {altType == 4 ? <Form.Group className="mb-4">
                <Form.Control as='textarea' rows={4} placeholder="Enter issue" className={`${contentError ? 'is-invalid' : ''}`} value={contentValue} onChange={(e)=>setContent(e.target.value)}></Form.Control>
                {contentError ? <span className='invalid-feedback'>This Field is Required.</span> : null}
            </Form.Group> : null}

            <Alert variant="warning" className="border border-warning rounded-1 border-2">
                <p className="fw-medium mb-0">Apologies for any inconvenience! We want to ensure a smooth experience. Could you share more details about the technical issues you've encountered? Our team is here to assist.</p>
            </Alert>
            <div className="d-flex">
                <Button variant="secondary" onClick={OPEN_INTERCOM}>Chat Now</Button>
                <Link to={HUBSPOT_URL} target="_blank" className="btn btn-primary ms-3">Book A Call</Link>
            </div>
        </div>
}
export default TechnicalIssue;
