export const googleAnaytic = (formData) => { return new HttpClient().post(`${GOOGLE_ANALYTIC}`, formData); }

export const imageDownload = (ownerId) => { return new HttpClient().get(`${IMAGE_DOWNLOAD}/${ownerId}`); }

