export const autoCompleteSearch = (keyword, slectedTags = '') => { return new HttpClient().post(`${MODERATION_POST_GET_AUTO_COMPLETE}`, { keyword: keyword, wallId: localStorage.getItem('wallId'), slectedTags, type: 3 }) }

export const getAutoModList = () => {
    return (dispatch) => {
        new HttpClient().post(`${AUTOMOD_LIST}`, { ownerId: localStorage.getItem('ownerId'), wallId: localStorage.getItem('wallId') })
            .then((res) => {
                const { response, status } = res.data;
                dispatch({ type: AUTO_MOD_LIST, payload: response, });
            });
    };
};


