import React from "react";
import { Button, Image } from "react-bootstrap"
import { baseUrl } from "../../../../../Assets/AssetsHelper";
const Expensive = ({offerRequest, acceptOffer}) => {
    return <div className="text-center py-4">
                <Image className="mb-5" src={baseUrl('media/images/gift-box.png')} alt="" width="100" height="100"  fluid/> 
                <p className="text-muted mb-4">We understand cost is important and we want to ensure that our services are beneficial for all our customer's journeys.</p>
                <p className="fw-medium mb-5">Hence, we would like to offer you an exclusive <b>10%</b> discount on your next billing cycle. Claim Now!</p>
                <Button className={offerRequest ? 'spinner' : ''} onClick={acceptOffer}>Accept This Offer</Button>
            </div>
}
export default Expensive;
