
import { useEffect } from 'react';
import { Routes, Route, useNavigate, useLocation, useParams } from "react-router-dom";
import { HOST_URL } from '../../../actions/api';
import { logOutAccount, loginWithShopify_guest } from '../../../actions/loginActions';
import { Loader } from '../../Layout/Main/CommonComponents/Loader';


export const SyncProducts = () => {
    const navigate = useNavigate()
    useEffect(() => { navigate(`/content/products`) }, [])

    return <div className='h-100vh w-100 f-center'><Loader /></div>
}

export const AutoLogin = () => {
    const params = useParams();
    const isAdmin = window.location.href.includes('admin')
    if (isAdmin) localStorage.setItem("isAdmin", 1)
    else localStorage.setItem("isAdmin", 0)
    if (params.token) {
        localStorage.setItem("token", atob(params.token))
        window.location.href = `${HOST_URL}home`
    }
    return <div className='h-100vh w-100 f-center'><Loader /></div>
}

export const ShopifyLogin = () => {
    const location = useLocation();
    const navigate = useNavigate()

    const removeNullParams = (obj) => {
        return Object.fromEntries(
            Object.entries(obj).filter(([_, value]) => value !== null && value !== undefined)
        );
    };

    useEffect(() => {
        const searchParams = new URLSearchParams(location.search);
        const params = {
            code: searchParams.get('code'),
            embedded: searchParams.get('embedded'),
            hmac: searchParams.get('hmac'),
            host: searchParams.get('host'),
            id_token: searchParams.get('id_token'),
            locale: searchParams.get('locale'),
            session: searchParams.get('session'),
            shop: searchParams.get('shop'),
            timestamp: searchParams.get('timestamp'),
            guestUseShopify: 'guest'
        };

        console.log(params,'paramsparamsparamsparamsparams-11111')
        const cleanParams = removeNullParams(params);

        loginWithShopify_guest(cleanParams, navigate);
        navigate('/home')
    }, []);
    return <></>;
}

export const LoadHomePage = () => {
    const navigate = useNavigate()
    useEffect(() => {
        navigate('/home')
    }, [])

    return <></>
}

export const ShopifyInstall = () => {
    useEffect(() => {
        localStorage.clear();
    }, [])
    return <></>
}

export const UserLogout = () => {
    const navigate = useNavigate()
    useEffect(() => {
        localStorage.clear()
        navigate('/accounts/login')
    }, [])
    return <></>
}

export const HomeRedirect = () => {
    const navigate = useNavigate()
    useEffect(() => {
        navigate('/home')
    }, [])
    return <></>
}