import React, { Component, Suspense } from 'react';
import { connect } from 'react-redux';
import withRouter from '../../../routes/WithRouter';
import { onBoardingSteps } from '../../../../utils';
import { TAGSHOP_USER_HOME_DATA } from '../../../../actions/sceneActions';
const Dashboard = React.lazy(() => import("./Dashboard"));

class Home extends Component {
 
    dashBoardSteps = () => {
       let { stepData } = this.props;
       if (!stepData) {
           return false;
       }
       const requiredSteps = ['FEED_CREATED', 'PRODUCT_ADDED', 'WEBSITE_VISITED', 'FEED_CUSTOMIZED', 'CUSTOMIZATION_SAVED', 'EMAIL_VISITED', 'EMAIL_PUBLISHED', 'EMAIL_PLATFORM_SELECTED', 'SHOPON_VISITED', 'SHOPON_BIO_ADDED', 'SHOPON_LINK_COPIED'];
       stepData = JSON.parse(stepData)
       return requiredSteps.every(step => stepData[step] === true);
   };

       render() {
              const { userData, navigate } = this.props;
              let login_number = userData?.user?.login_number
              return <div className="full-content home_ position-relative">
                   {localStorage.getItem('shopifyLogin') ? userData?.collaborator && Object.keys(userData.collaborator).length > 0 ? 
                   <Suspense fallback={null}><Dashboard login_number={login_number} navigate={navigate}/></Suspense> : userData ? onBoardingSteps() ? this.dashBoardSteps() ? <Dashboard login_number={login_number} navigate={navigate} /> : login_number > 2 ? <Dashboard login_number={login_number} navigate={navigate} /> : navigate('/steps') : navigate('/onboarding') : <></> : <Dashboard login_number={login_number} navigate={navigate}/> } 
              </div>
       }
}

const mapStateToProps = state => {
       return {
              stepData: state.authorized?.userData?.user_data?.user?.extra_data,
              userData: state.authorized?.userData?.user_data,
       }
}

const mapDispatchToProps = dispatch => {
       return {
              TAGSHOP_USER_HOME_DATA: () => dispatch(TAGSHOP_USER_HOME_DATA()),
       }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Home))