import React, { Component } from 'react';
import { Button, Modal, Table, Image } from 'react-bootstrap';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import moment from 'moment';

class ViewInvoice extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    handleClose = () => {
        this.props.viewInvoiceCallback(false)
    };



    render() {
        const { modalToggle, viewInvoiceData, clickToInvoiceDownload, downloadRequest } = this.props
        return <Modal show={modalToggle} onHide={this.handleClose}
            aria-labelledby="contained-modal-title-vcenter"
            size="xl"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Invoice</Modal.Title>
            </Modal.Header>
            <Modal.Body className="p-0">
                <div className="container" id="printInvoice">
                    <div className="row justify-content-center py-2 py-xl-3">
                        <div className="col-xl-9">
                            <div className="d-flex justify-content-between pb-3 pb-lg-4 flex-column flex-md-row">
                                <span className="logo_full_ my-2 w-50 text-gray-800" style={{ maxWidth: 200 }}>
                                    <Image data-mode="light" src={baseUrl('media/logo/tagshop-poweredby.svg')} height={46} width={150} alt={'Tagshop'} fluid/>
                                    <Image data-mode="dark" src={baseUrl('media/logo/tagshop-poweredby-light.svg')} height={46} width={150} alt={'Tagshop'} fluid/>
                                </span>
                                <div className="d-flex flex-column align-items-md-end px-0 my-2">
                                    <span className="d-flex flex-column align-items-md-end text-gray-500 fw-medium">
                                        <span className="mb-1">Social Scape Tech LLP, C-2, Panchsheel Colony</span>
                                        <span>305004 Jaipur, Rajasthan India</span>
                                    </span>
                                </div>
                            </div>
                            <div className="border-bottom w-100"></div>
                            <div className="d-flex justify-content-between pt-3 pt-lg-4">
                                <div className="d-flex flex-column flex-root">
                                    <span className="fw-bolder mb-2 text-uppercase">Date</span><span className="text-gray-500 fw-medium">{moment(viewInvoiceData.invoiceDate).format("MMM DD , YYYY")}</span>
                                </div>
                                <div className="d-flex flex-column flex-root">
                                    <span className="fw-bolder mb-2 text-uppercase">Invoice No.</span><span className="text-gray-500 fw-medium">{viewInvoiceData.invoiceView.invoiceId}</span>
                                </div>
                                <div className="d-flex flex-column flex-root">
                                    <span className="fw-bolder mb-2 text-uppercase">Invoice To</span>
                                    <span className="text-gray-500 fw-medium">
                                        {viewInvoiceData.invoiceView.customerName}
                                    </span>
                                    <span className="text-gray-500 fw-medium">
                                        {viewInvoiceData.invoiceView.address1Value},
                                        {viewInvoiceData.invoiceView.address2Value}
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row justify-content-center py-3 py-xl-4">
                        <div className="col-xl-9">
                            <div className="table-responsive">
                                <table className="table table-borderless">
                                    <thead>
                                        <tr>
                                            <th className="ps-0 fw-bold text-gray-500 text-uppercase border-start-0 py-2 border-bottom text-nowrap">Description</th>
                                            <th className="text-end pe-0 fw-bold text-gray-500 text-uppercase border-end-0 border-bottom py-2 text-nowrap" style={{ width: 120 }}>Amount</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr className="fw-bold">
                                            <td className="ps-0 py-2 text-nowrap border-start-0">{viewInvoiceData.invoiceView.item} 
                                                {viewInvoiceData?.invoiceView?.invoiceDueDate && <span className="text-gray-500 fw-medium"> (Next Renewal Date {moment(viewInvoiceData.invoiceView.invoiceDueDate).format("MMM DD , YYYY")})</span>}
                                            </td>
                                            <td className=" pe-0 py-2 text-end text-nowrap border-end-0" style={{ maxWidth: 100 }}>{viewInvoiceData.invoiceView.currencyValue}{viewInvoiceData.invoiceView.amount}</td>
                                        </tr>
                                        {viewInvoiceData.viewInvoiceData && parseInt(viewInvoiceData.viewInvoiceData.vat) > 0 ? <tr className="fw-bold border-bottom-0">
                                            <td className="border-top-0 ps-0 py-2 text-nowrap text-end border-start-0">18.00% IGST</td>
                                            <td className=" border-top-0 pe-0 py-2 text-end text-nowrap border-end-0" style={{ width: 120 }}>{viewInvoiceData.invoiceView.currencyValue}{viewInvoiceData.gstAmount}</td>
                                        </tr> : null}
                                        <tr className="fw-bold border-bottom-0">
                                            <td className="border-top-0 ps-0 py-2 text-nowrap text-end border-start-0">Total</td>
                                            <td className=" border-top-0 pe-0 py-2 text-end text-nowrap border-end-0" style={{ width: 120 }}>{viewInvoiceData.invoiceView.currencyValue}{viewInvoiceData.amount}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                    <div className="w-100">
                        <div className="row justify-content-center py-2 py-xl-3">
                            <div className="col-xl-9">
                                <Table borderless responsive>
                                    <thead>
                                        <tr>
                                            <th className="ps-0 fw-medium text-gray-500 text-uppercase border-bottom py-2 text-nowrap">Transaction Date</th>
                                            <th className="text-end fw-medium text-gray-500 text-uppercase border-bottom py-2 text-nowrap">Gatway</th>
                                            <th className="text-end fw-medium text-gray-500 text-uppercase border-bottom py-2 text-nowrap">Transaction ID</th>
                                            <th className="text-end pe-0 fw-medium text-gray-500 text-uppercase border-bottom py-2 text-nowrap">Total Amount</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr className="fw-bold">
                                            <td className="ps-0 py-3 text-nowrap">{moment(viewInvoiceData.invoiceDate).format("MMM DD , YYYY")}</td>
                                            <td className="text-end py-3 text-nowrap">{viewInvoiceData.source == 1 ? `Stripe` : viewInvoiceData.source == 0 ? `2Checkout` : ``}</td>
                                            <td className="text-end py-3 text-nowrap">{viewInvoiceData.invoiceView.invoiceId}</td>
                                            <td className=" pe-0 py-3 text-end text-nowrap">{viewInvoiceData.invoiceView.currencyValue}{viewInvoiceData.amount}</td>
                                        </tr>
                                    </tbody>
                                </Table>
                            </div>
                        </div>
                    </div>
                    <div className="row justify-content-center py-4 py-xl-5">
                        <div className="col-xl-9">
                            <div className="d-flex justify-content-end">
                                <Button type="button" className={`btn btn-primary ${downloadRequest ? 'spinner' : ''}`} onClick={clickToInvoiceDownload(viewInvoiceData.invoiceId, viewInvoiceData.invoiceView.invoiceId)}>Download Invoice</Button>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal.Body>
        </Modal>

    }
}

export default ViewInvoice;