import React from "react";
import { Link } from "react-router-dom";
import { HUBSPOT_URL } from "../../../../../actions/api";
import { baseUrl } from "../../../../../Assets/AssetsHelper";
import { Button, Image, Form, Alert } from "react-bootstrap"
import { OPEN_INTERCOM } from "../../../../../utils";


const HardToUse = () => {
    return <div className="text-center py-4">
            <Image className="mb-5" src={baseUrl('media/images/user-laptop.png')} alt="" width="100" height="100"  fluid/> 
            <p className="text-muted mb-5">We understand that ease of use is crucial. Would you like to book a free call with our experts who will guide you through the setup and handle all your queries?</p>
            <Alert variant="warning" className="border border-warning rounded-1 border-2">
                <p className="fw-medium mb-0">Apologies for any inconvenience! We want to ensure a smooth experience. Could you share more details about the technical issues you've encountered? Our team is here to assist.</p>
            </Alert>
            <div className="f-center">
                <Button variant="secondary" className="mx-2" onClick={OPEN_INTERCOM}>Chat Now</Button>
                <Link to={HUBSPOT_URL} target="_blank" className="btn btn-primary mx-2">Book A Call</Link>
            </div>
        </div>
}
export default HardToUse;
