import React, { Suspense, useEffect } from 'react'
import { Routes, Route, useNavigate, useLocation, useParams } from "react-router-dom";
import { Loader } from '../Layout/Main/CommonComponents/Loader';
import { useDispatch, useSelector } from 'react-redux';
import { SHOPIFY_PRODUCT_SYNC } from '../../actions/productAction';
import { getAllNotification } from '../../actions/notificationAction';
import { togglePriceSection } from '../../actions/show_modal_Action';
import { SyncProducts, AutoLogin, ShopifyLogin, LoadHomePage, ShopifyInstall, UserLogout, HomeRedirect } from './ExtendRoutes';
import PageTitle from './pageTitle'
import ErrorFound from '../ErrorFound';
import PricePlan from '../Layout/Main/PricePlan/PricePlan';
import PlanExpire from '../Layout/Main/PricePlan/PlanExpire'
import PaymentFailed from '../Layout/Main/PricePlan/PaymentFailed';
import StripeCallback from '../Layout/Main/PricePlan/StripeCallback';
import ShopifyPlanCallback from '../Layout/Main/PricePlan/ShopifyPlanCallback';
import PlanLimitExceed from "./PlanLImitExceed";
import Home from '../Layout/Main/Home';
import Authorization from '../../utils/Authorization';
import AccountInfo from '../Layout/Main/AccountInfo/AccountInfo';
import Profile from '../Layout/Main/Profile/Profile';
import SocialAccount from '../Layout/Main/SocialAccount/SocialAccount';
import CancelPlan from '../Layout/Main/CancelPlan';
import PaymentSuccess from '../Layout/Main/PricePlan/PaymentSuccess';
import UgcRightsAction from '../Layout/Main/UgcRightsAction';
import ManualUpload from '../Layout/Main/Feeds/ManualUpload';
import ManualUploadModal from '../Layout/Main/Feeds/ManualUpload/ManualUploadModal';
import ColabSignUp from "../Authentication/ColabSignUp";

const OnBoardingSteps = React.lazy(() => import("../Layout/Main/Home/OnBoardingSteps")); 
const Steps = React.lazy(() => import("../Layout/Main/Home/Steps"));
const Moderation = React.lazy(() => import("../Layout/Main/Moderation"));
const Productpage = React.lazy(() => import('../Layout/Main/ProductCatalog'));

const AddFeed = React.lazy(() => import('../Layout/Main/Feeds/AddFeed'));
const CreateFeed = React.lazy(() => import('../Layout/Main/Feeds/AddFeed/CreateFeed'));

const ManageFeed = React.lazy(() => import('../Layout/Main/Feeds/ManageFeed'));
const AppsMore = React.lazy(() => import('../Layout/Main/AppsMore'));
const EmailCampaign = React.lazy(() => import('../Layout/Main/EmailCampaign'));

const ShopOnBio = React.lazy(() => import('../Layout/Main/ShopOnBio'));
const Website = React.lazy(() => import('../Layout/Main/Website'));
const MorePublish = React.lazy(() => import('../Layout/Main/MorePublish'));

const Creators = React.lazy(() => import('../Layout/Main/Creators'));
const LimitExceed = React.lazy(() => import('../Layout/Main/LimitExceed'));
const Analytics = React.lazy(() => import('../Layout/Main/Analytics'));
const ShareAccount = React.lazy(() => import('../Layout/Main/SocialAccount/ShareAccount'));
const ManualUploadWrap = React.lazy(() => import('../Layout/Main/Feeds/ManualUpload/ManualUploadWrap'));
const ManualHead = React.lazy(() => import('../Layout/Main/Feeds/ManualUpload/ManualHead'));
const Collaborator = React.lazy(() => import('../Layout/Main/Collaborator/Collaborator'))
const ManageCollaborator = React.lazy(() => import('../Layout/Main/Collaborator/ManageCollaborator'))
const PageNotFound = React.lazy(() => import('../Layout/Main/PageNotFound'))
const Authentication = React.lazy(() => import("../Authentication"));
const AutoPilot = React.lazy(() => import("../Layout/Main/AutoPilot"));



const SyncProductShopify = () => {
  const navigate = useNavigate()
  const params = useParams();
  const fromOnboarding = JSON.parse(localStorage.getItem("onboarding"))
  useEffect(() => {
    if (params.storeId) {
      if (fromOnboarding) { SHOPIFY_PRODUCT_SYNC(params.storeId, () => navigate(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}?ob_success=true`)); localStorage.removeItem("onboarding") }
      else SHOPIFY_PRODUCT_SYNC(params.storeId, () => navigate(`/content/products`))
    }
    else navigate(`/content/products`)
  }, [])

  return <div className='h-100vh w-100 f-center'><Loader /></div>
}
const Wrapper = ({ children }) => <Suspense fallback={null}> <ErrorFound>{children}</ErrorFound></Suspense>


export default function Navigation(props) {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  let location = useLocation()
  const WallExceed = JSON.parse(localStorage.getItem('wallLimitExceed'))
  const accExpire = JSON.parse(localStorage.getItem('accountExpire'))
  var userDetail = props.authorized && props.authorized.userData && props.authorized.userData.user_data ? props.authorized.userData.user_data : '';
  const { planLimitExceed } = useSelector(state => state.authorized)
  useEffect(() => {
    if (window.location.pathname == '/v2') navigate('/home')
    dispatch(getAllNotification('all', ''))
  }, [])

  useEffect(
    () => {
      dispatch(togglePriceSection(false, 2))
    },
    [location]
  )


  return <>
    <PageTitle />
    {/* <PricePlan /> */}
    {planLimitExceed && !(window.location.pathname.includes("/price") || window.location.pathname.includes("shopify/success") || window.location.pathname.includes("/paymentPaidCallback")) ? <PlanLimitExceed /> : <></>}
    <Routes>
      {!WallExceed ?
        !accExpire ?
          <>
            <Route exact path="/" element={<LoadHomePage />} />
            <Route exact path="/walls/authcallback" element={<Wrapper><Authorization /></Wrapper>} />
            <Route exact path="/feeds/authCallback" element={<Wrapper><Authorization /></Wrapper>} />
            <Route exact path="/home" element={<Wrapper><Home /></Wrapper>} />
            <Route exact path="/steps" element={<Wrapper><Steps /></Wrapper>} />
            <Route exact path="/onboarding" element={<Wrapper><OnBoardingSteps userDetail={userDetail} /></Wrapper>} />
            <Route exact path="/transactions" element={<Wrapper><AccountInfo /></Wrapper>} />
            <Route exact path="/social-account" element={<Wrapper><SocialAccount /></Wrapper>} />
            {/* <Route exact path="/accounts/verifyemail/:id" element={<HomeRedirect />} /> */}
            <Route exact path="/profile" element={<Wrapper><Profile /></Wrapper>} />
            <Route exact path="/shopify/syncProducts/:storeId" element={<SyncProductShopify />} />
            <Route exact path="/shopify/syncProducts" element={<SyncProducts />} />
            <Route path="/Shopify/guest" element={<ShopifyLogin />} />
            <Route exact path="/creators" element={<Wrapper><Creators userDetail={userDetail} /></Wrapper>} />
            <Route exact path="/price" element={<Wrapper><PricePlan /></Wrapper>} />
            <Route exact path="/transactions/cancel" element={<Wrapper><CancelPlan /></Wrapper>} />
            <Route exact path="*" element={<LoadHomePage />} />

            <Route exact path="/permission-denied" element={<Wrapper><PageNotFound /></Wrapper>} />

            <Route exact path="/content/moderation/:id" element={<Wrapper><Moderation userDetail={userDetail} /></Wrapper>} />
            <Route exact path="/content/moderation" element={<Wrapper><Moderation userDetail={userDetail} /></Wrapper>} />
            <Route exact path="/content/moderation/:id/feed/:feedId" element={<Wrapper><Moderation userDetail={userDetail} /></Wrapper>} />
            <Route exact path="/content/moderation/:wallID/feed/:feedId/status/:statusId/notificationId/:notificationId" element={<Wrapper><Moderation userDetail={userDetail} /></Wrapper>} />
            <Route path="/content/addfeed/:wallId">
              <Route path="" element={<Wrapper><AddFeed /></Wrapper>} />
              <Route path=":networkId" element={<Wrapper><CreateFeed /></Wrapper>} />
            </Route>
            <Route path="/content/reviewhub/:wallId/:networkId" element={<Wrapper><CreateFeed /></Wrapper>} />
            <Route exact path="/content/managefeed/:id" element={<Wrapper><ManageFeed /></Wrapper>} />
            <Route exact path="/content/managefeed/" element={<Wrapper><ManageFeed /></Wrapper>} />
            <Route exact path="/content/products" element={<Wrapper><Productpage /></Wrapper>} />
            <Route exact path="/more-apps/:id" element={<Wrapper><AppsMore customization={false} /></Wrapper>} />
            <Route exact path="/more-apps" element={<Wrapper><AppsMore customization={false} /></Wrapper>} />
            <Route exact path="/manual-upload" element={<Wrapper><ManualHead /></Wrapper>} />
            <Route exact path="/manual-upload/feed" element={<Wrapper><ManualUploadWrap /></Wrapper>} />
            {/* <Route exact path="/manual-upload" element={<Wrapper><ManualUploadModal /></Wrapper>} /> */}

            <Route exact path="/website/publish/:id" element={<Wrapper><Website /></Wrapper>} />
            <Route exact path="/website/publish" element={<Wrapper><Website /></Wrapper>} />

            <Route exact path="/website/shopify/:id" element={<Wrapper><Website /></Wrapper>} />
            <Route exact path="/website/shopify" element={<Wrapper><Website /></Wrapper>} />
            <Route exact path="/website/bigcommerce/:id" element={<Wrapper><Website /></Wrapper>} />
            <Route exact path="/website/bigcommerce" element={<Wrapper><Website /></Wrapper>} />

            <Route exact path="/emailCampaign/publish/:id" element={<Wrapper><EmailCampaign /> </Wrapper>} />
            <Route exact path="/email-campaign/publish/:id" element={<Wrapper><EmailCampaign /> </Wrapper>} />
            <Route exact path="/email-campaign/publish" element={<Wrapper><EmailCampaign /> </Wrapper>} />
            <Route exact path="/shopon-bio/preview/:id" element={<Wrapper><ShopOnBio /> </Wrapper>} />
            <Route exact path="/shopon-bio/preview/" element={<Wrapper><ShopOnBio /> </Wrapper>} />
            <Route exact path="/more-publish/" element={<Wrapper><MorePublish /> </Wrapper>} />

            <Route exact path="/wall/extrawallfeeds" element={<HomeRedirect />} />
            {/* <Route exact path="/analytic/:wallID" element={<Wrapper><AnalyticNew /></Wrapper>} /> */}
            <Route exact path="/analytics" element={<Wrapper><Analytics /></Wrapper>} />
            <Route exact path="/collaborator" element={<Wrapper><Collaborator /></Wrapper>} />
            <Route exact path="/collaborator/add" element={<Wrapper><ManageCollaborator /></Wrapper>} />
            <Route path="/collaborator/edit/:id" element={<Wrapper><ManageCollaborator /></Wrapper>} />
            <Route exact path="/autopilot" element={<Wrapper><AutoPilot planId={userDetail?.UserPlan?.planId} navigate={navigate} ownerId={userDetail?.user?.id}/></Wrapper>} />

          </>
          :
          <>
            <Route exact path="*" element={<Wrapper><PlanExpire /></Wrapper>} />
          </>
        :
        <>
          <Route exact path="/wall/extrawallfeeds" element={<Wrapper><LimitExceed /></Wrapper>} />
          <Route exact path="*" element={<Wrapper><LimitExceed /></Wrapper>} />
          <Route exact path="/transactions" element={<Wrapper><AccountInfo /></Wrapper>} />
          <Route exact path="/price" element={<Wrapper><PricePlan /></Wrapper>} />
          <Route exact path="/content/moderation/:id" element={<Wrapper><Moderation userDetail={userDetail} /></Wrapper>} />
          <Route exact path="/content/moderation" element={<Wrapper><Moderation userDetail={userDetail} /></Wrapper>} />
          <Route exact path="/content/moderation/:id/feed/:feedId" element={<Wrapper><Moderation userDetail={userDetail} /></Wrapper>} />
          <Route exact path="/content/moderation/:wallID/feed/:feedId/status/:statusId/notificationId/:notificationId" element={<Wrapper><Moderation userDetail={userDetail} /></Wrapper>} />
          <Route exact path="/price/cancel" element={<Wrapper><CancelPlan /></Wrapper>} />
          <Route exact path="/social-account" element={<Wrapper><SocialAccount /></Wrapper>} />
          <Route exact path="/profile" element={<Wrapper><Profile /></Wrapper>} />

        </>
      }
      <Route exact path="/accounts/expire" element={<Wrapper><PlanExpire /></Wrapper>} />
      <Route exact path="/accounts/paymentPaidCallback" element={<StripeCallback />} />
      <Route exact path="/price/success" element={<Wrapper><PaymentSuccess /></Wrapper>} />
      <Route exact path="/price/failed" element={<Wrapper><PaymentFailed /></Wrapper>} />
      <Route exact path="/price/shopify/success" element={<PaymentSuccess dispatch={dispatch} />} />
      <Route exact path="/redirectlogin/:token" element={<AutoLogin />} />
      <Route exact path="/redirectlogin/:token/:isAdmin" element={<AutoLogin />} />
      <Route exact path="/ugc_right/:id" element={<UgcRightsAction />} />
      {/* <Route exact path="/ugc_right/authCallback" element={<UgcFallback />} /> */}
      <Route exact path="/logout" element={<UserLogout />} />
      <Route exact path="/accounts/register" element={<HomeRedirect />} />
      <Route exact path="/accounts/shopifypaymentcomplete" element={<ShopifyPlanCallback />} />
      <Route exact path="/connection/:id" element={<Wrapper><ShareAccount /></Wrapper>} />
      <Route path="/collaborator/request/:ownerToken/:collaboratorToken" element={<Authentication><ColabSignUp history={navigate} /></Authentication>}></Route>

      <Route path="/Shopify/install" element={<ShopifyInstall />} />

    </Routes>

  </>
}