import React, { Component } from 'react';
import { connect } from "react-redux";
import { Button, Row, Col, Form } from 'react-bootstrap';
import ProfileImage from '../CommonComponents/ProfileImage/ProfileImage';
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import { generateRandomArray } from '../../../../utils';


class ViewProfile extends Component {

    editProile = event => {
        this.props.editProileCallback(event);
    }
    render() {
        const { userData, loading } = this.props
        return <div className="w-100">
            <Row>
                <Col xs={8} className="d-flex align-items-center mb-4 mb-xs-0">
                    <div className="me-3 me-xxl-4">
                        {loading ?
                            <ProfileImage className={'symbol h-90px w-90px position-relative rounded-2 overflow-hidden'}
                                imageSize={90}
                                symbolClass={'symbol-label fw-bold display-1 bg-primary'}
                                name={userData.name} profileUrl={userData.picture} networkId={null} />
                            :
                            <div className='h-90px w-90px rounded-2 sk_rect'></div>
                        }
                    </div>
                    <div className="d-flex flex-column my-2">
                        {loading ?
                            <>
                                <div className="d-flex align-items-center mb-2">
                                    <h6 className="fs-4  text-hover-primary mb-0 me-1">{userData.name}</h6>
                                    {userData.access == 1 ? <span className="fs-3 text-primary">
                                        <FontAwesomeIcon type={'solid'} icon={'badge-check'} iconClass="me-1"/>
                                    </span> : null}
                                </div>
                                <div className="d-flex flex-wrap pe-2">
                                    <p className="d-flex align-items-center text-decoration-none text-hover-primary mb-2 fw-medium">
                                        <FontAwesomeIcon type="solid" icon={'envelope'} iconClass="me-1 text-muted"/>
                                        {userData.emailId}
                                    </p>
                                </div>
                            </>
                            :
                            <>
                                <div className='sk_line sk_line_height_16 my-3' style={{ width: 100 }}></div>
                                <div className='sk_line sk_line_height_12 my-3' style={{ width: 150 }}></div>
                            </>
                        }
                    </div>
                </Col>
            </Row>
            <hr></hr>
            <div className="d-flex align-items-center justify-content-between flex-wrap py-1 mb-3">
                <div className="">
                    <h3 className="fs-6">Personal Info</h3>
                </div>
                <div className="d-flex">
                    <Button variant='secondary' onClick={() => this.editProile(true)} size={'sm'} disabled={!loading}>Edit Profile</Button>
                </div>
            </div>
            {loading ?
                <div>
                    <Row className="mb-4 mb-xl-5">
                        <Form.Label className='col-sm-4'>Full Name</Form.Label>
                        <Col sm={8}>
                            <span className="fw-bold fs-8">{userData.name}</span>
                        </Col>
                    </Row>
                    <Row className="mb-4 mb-xl-5">
                        <Form.Label className='col-sm-4'>Email</Form.Label>
                        <Col sm={8}>
                            <span className="fw-bold fs-8 me-2">{userData.emailId}</span>
                            <span className={`badge ${userData.access == 1 ? `badge-light-success` : `badge-light-warning`}`}>{userData.access == 1 ? `Verified` : `Pending`}</span>
                        </Col>
                    </Row>
                    <Row className="mb-4 mb-xl-5">
                        <Form.Label className='col-sm-4'>Industry</Form.Label>
                        <Col sm={8}>
                            <span className="fw-semibold fs-8">{userData.industry ? userData.industry : '--'}</span>
                        </Col>
                    </Row>
                    <Row className="mb-4 mb-xl-5">
                        <Form.Label className='col-sm-4'>Organization</Form.Label>
                        <Col sm={8}>
                            <span className="fw-semibold fs-8 text-hover-primary">{userData.organization ? userData.organization : '--'}</span>
                        </Col>
                    </Row>
                </div>
                :
                generateRandomArray(4).map((number, index) => (
                    <Row className="mb-4 mb-xl-5" key={index}>
                        <Form.Label className='col-sm-4'>
                            <div className='sk_line sk_line_height_12 mb-1' style={{ width: 100 }}></div>
                        </Form.Label>
                        <Col sm={8}>
                            <div className='sk_line sk_line_height_16 mb-1' style={{ width: 100 }}></div>
                        </Col>
                    </Row>
                ))}
            <div>

            </div>
            {loading ? userData.access == 2 ? <div className="d-flex alert alert-warning">
                <FontAwesomeIcon type="solid" icon={'circle-info'} iconClass="me-1 text-warning"/>
                <div className="d-flex flex-column flex-stack flex-grow-1">
                    <h4 className="fs-6 fw-medium mb-1">Your email needs to be verified!</h4>
                    <p className="mb-0">We have sent you and email with the verfication link to <strong>{userData.emailId}</strong>. Kindly click on the verfication link present in the email to start exploring UGC Suit further.</p>
                </div>
            </div> : null : <></>}
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authorized?.userData?.user_data?.user,
    }
}
export default connect(mapStateToProps)(ViewProfile);