import React, { Component } from 'react';
import $ from 'jquery'
import Swal from 'sweetalert2';
import { connect } from "react-redux";
import Select from 'react-select';
import { OverlayTrigger, Tooltip, Row, Col, Form, Button } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import UppyUpload from '../../../../utils/uppyUpload';
import { WASABI_PATH, PROFILE_UPDATE } from '../../../../actions/api';
import { DashboardModal } from "@uppy/react";
import withRouter from '../../../routes/WithRouter';
import HttpClient from '../../../../actions/http-client';
import { getUserInfo_afterChanges } from '../../../../actions/loginActions';
import { TOAST_ERROR, TOAST_SUCCESS } from '../../../../utils';

const options = [
    "Agency",
    "Automotive",
    "E-Commerce",
    "Education",
    "Entertainment",
    "Food & Beverage",
    "Government",
    "Non-Profit",
    "Retail",
    "Sports",
    "Travel",
    "Venues"
];

const uppyUploader = new UppyUpload()

class EditProfile extends Component {
    constructor(props) {
        super(props)
        var userProfile = {}
        if (props.userData && Object.keys(props.userData).length > 0) {
            userProfile = {
                profile: props.userData.picture,
                name: props.userData.name,
                emailId: props.userData.emailId,
                industry: props.userData.industry != "" && props.userData.industry ? { value: props.userData.industry, label: props.userData.industry } : null,
                organization: props.userData.organization,
            }
        }

        this.state = {
            isRequesting: false,
            profile: null,
            emailId: null,
            country_code: null,
            contact_no: null,
            industry: null,
            organization: null,
            department: null,
            selectedOption: null,
            uppyProfileOpen: false,
            contactError: false,
            ...userProfile,
            visiblePass: false,
            visibleConfirmPass: false,
            nameError: false,

        };
    }

    componentDidMount() {
        if (uppyUploader) {
            uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
                this.setState({ profile: `${WASABI_PATH}tmp/${result.id}.${result.ext}` }, () => {
                    setTimeout(() => {
                        uppyUploader.uppy.cancelAll()
                        this.setState({ uppyProfileOpen: false })
                    }, 300)
                })
            })
        }
    }

    componentWillReceiveProps(nextProps) {
        const { userData } = nextProps;
        this.setState({
            profile: userData.picture,
            name: userData.name,
            emailId: userData.emailId,
            industry: userData.industry != "" && userData.industry ? { value: userData.industry, label: userData.industry } : null,
            organization: userData.organization,
            department: userData.department
        })
    }


    onChangeName = event => this.setState({ name: event.target.value })

    onChangeOrganization = event => this.setState({ organization: event.target.value })

    onClickRemoveProfile = event => this.setState({ profile: "" })

    onClickUpdateProfile = event => {
        const { profile, name, industry, organization } = this.state;
        const fullNameRegex = /^[a-zA-Z0-9]+(([',. -][a-zA-Z0-9 ])?[a-zA-Z0-9]*)*$/;
        if (fullNameRegex.test(String(name).trim())) {
            const fullName = name.split(" ")
            const data = {
                "organization": organization,
                "industry": industry && Object.keys(industry).length > 0 && industry.value ? industry.value : "",
                "picture": profile,
                "firstName": fullName[0],
                "lastName": fullName[1] ? fullName[1] : "",
                "password": ""
            }
            this.setState({ isRequesting: true })

            new HttpClient().post(PROFILE_UPDATE, data).then((response) => {
                const { response_message } = response.data;
                TOAST_SUCCESS(response_message);
                this.props.editProileCallback(false)
                this.props.getUserInfo()
            }).catch(ex => {
                const { message } = ex.response.data
                TOAST_ERROR(message)
                this.setState({ isRequesting: false })
            })
        }

        else {
            this.setState({ nameError: true })
        }

    }

    onChangeIndustry = event => {
        this.setState({ industry: event })
    }

    handleClose = event => this.setState({
        uppyProfileOpen: false,
    })

    handleOpen = event => {
        this.setState({
            uppyProfileOpen: true,
        })
        $(".uppy-Dashboard-close").click(() => {
            uppyUploader.uppy.cancelAll()
            this.setState({ uppyProfileOpen: false })
        })
    }

    onClickDiscard = event => {
        Swal.fire({
            title: 'Are you sure?',
            text: "You want to discard.",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes, discard!'
        }).then((result) => {
            if (result.isConfirmed) {
                var self = this;
                const { userData, navigate } = self.props
                self.setState({
                    profile: userData.picture,
                    name: userData.name,
                    emailId: userData.emailId,
                    industry: userData.industry != "" && userData.industry ? { value: userData.industry, label: userData.industry } : null,
                    organization: userData.organization,
                })

                this.props.editProileCallback(false);

                navigate("/profile")
            }
        })
    }

    render() {
        const { isRequesting } = this.state;
        const { userData } = this.props;
        const { profile, name, emailId, industry, organization, nameError } = this.state;

        return <>{userData && Object.keys(userData).length > 0 && emailId ? <>
            <h2 className="fs-6 mb-5">Edit Personal Info</h2>
            <Row className="mb-3 mb-xl-4">
                <label className="col-sm-4 fw-semibold text-muted mb-2">Avatar</label>
                <Col className="col-sm-8">
                    <div className="d-flex align-items-center">
                        <div className="me-3 me-xxl-4">
                            <div className="symbol h-90px w-90px position-relative rounded-2 overflow-hidden">
                                {profile && profile != '' ? <img src={profile} alt={userData.name} height={90} width={90} className="img-fluid object-center-cover" load="lazy" />
                                    : <span className="symbol-label bg-primary fw-bold display-1 text-uppercase">{userData.name.charAt(userData.name)}</span>}

                            </div>
                        </div>
                        <div className="d-flex align-items-center flex-wrap">
                            <div className="d-flex flex-column align-items-center flex-nowrap mb-0">
                                <OverlayTrigger placement="top" overlay={<Tooltip>Edit</Tooltip>}>
                                    <Button variant='icon' size='sm' className="btn-active-primary my-1" onClick={this.handleOpen}>
                                        <FontAwesomeIcon type="solid" icon={'pen'} />
                                    </Button>
                                </OverlayTrigger>
                                <OverlayTrigger placement="top" overlay={<Tooltip>Remove</Tooltip>}>
                                    <Button variant='icon' size='sm' className="btn-active-primary my-1" onClick={this.onClickRemoveProfile}>
                                        <FontAwesomeIcon type="solid" icon={'trash-can'} />
                                    </Button>
                                </OverlayTrigger>
                            </div>
                        </div>
                    </div>
                    <div className="form-text mt-2">Allowed file types: png, jpg, jpeg.</div>
                </Col>
            </Row>
            <Row className="mb-4 mb-xl-5">
                <Form.Label className="col-sm-4">Full Name <strong className="text-danger">*</strong></Form.Label>
                <Col sm={8}>
                    <Form.Control name="fname" className={`${nameError ? "is-invalid" : ''}`} placeholder="First name" value={name} onChange={this.onChangeName} />
                    <div className="invalid-feedback">Please enter valid name.</div>
                </Col>
            </Row>

            <Row className="mb-4 mb-xl-5">
                <Form.Label className="col-sm-4">Email <strong className="text-danger">*</strong></Form.Label>
                <Col sm={8}>
                    <Form.Control name="email" className="form-control form-control-lg" placeholder="email" value={emailId} disabled />
                </Col>
            </Row>

            <Row className="mb-4 mb-xl-5">
                <Form.Label className="col-sm-4">Industry</Form.Label>
                <Col sm={8}>
                    <Select className="selectpicker selectpicker-lg" menuPlacement="auto" classNamePrefix="selectpicker" onChange={this.onChangeIndustry} options={options.map(option => ({ value: option, label: option }))} defaultValue={industry} isSearchable={false} />
                </Col>
            </Row>
            <Row className="mb-4 mb-xl-5">
                <Form.Label className="col-sm-4">Organization</Form.Label>
                <Col sm={8}>
                    <Form.Control name="organization" className="form-control form-control-lg" placeholder="Enter Organization Name" value={organization} onChange={this.onChangeOrganization} />
                </Col>
            </Row>
            <div className="d-flex align-items-center justify-content-end flex-wrap pt-3">
                <Button variant="secondary" className="me-3" onClick={this.onClickDiscard}>Discard</Button>
                <Button className={`${isRequesting ? `spinner disabled` : ``}`} onClick={this.onClickUpdateProfile}>Save Changes</Button>
            </div>
        </> : null
        }

            {this.state.uppyProfileOpen ?
                <DashboardModal
                    id="background-image"
                    closeModalOnClickOutside
                    closeAfterFinish
                    open={this.state.uppyProfileOpen}
                    onRequestClose={this.handleClose}
                    uppy={uppyUploader.uppy}
                    hideUploadButton={false}
                    autoOpenFileEditor={false}
                    plugins={[
                        "Instagram",
                        "GoogleDrive",
                        "Url",
                        "Webcam"
                    ]}


                    animateOpenClose={true}
                    proudlyDisplayPoweredByUppy={false}
                    inline={false}
                    showProgressDetails={true}
                    height={750}
                    browserBackButtonClose={true}
                    showRemoveButtonAfterComplete={false}
                />
                : null}
        </>

    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authorized?.userData?.user_data?.user,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getUserInfo: () => dispatch(getUserInfo_afterChanges())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(EditProfile));

