
import { baseUrl, ImagePlaceholder, ProductPlaceholder } from '../../../../../Assets/AssetsHelper';
import { Image } from 'react-bootstrap';
import { GET_MEDIA_NAME, getImageWidthHeight } from '../../../../../utils';
import { useEffect, useState } from 'react';


const ImageType = ({ mediaClass, size, height, width, url, alt, isProduct, handleAllImageError ,item, isCover}) => {


  const [padding, setPadding] = useState(size ? size : 100)

  const onMediaLoad = async () => {
  if(size){
    setPadding(size)
  }else{
    if(height && height != "" && width && width != ""){
      setPadding((height * 100) / width)
    }else{
      try {
        const { width, height } = await getImageWidthHeight(url);
          setPadding((height * 100) / width)
      } catch (error) {
          setPadding(100)
      }
    }
  }
}


  useEffect(() => {
    onMediaLoad()
  }, [size])

  const onErrorImageUpdate = event => {
    event.target.src = isProduct?ProductPlaceholder:ImagePlaceholder
  }
  const handleError = (event) => {
    if (handleAllImageError) {
      handleAllImageError(event);
    }
    onErrorImageUpdate(event);
  };


  return <div className={`${mediaClass} overflow-hidden position-relative`} style={{ paddingBottom: `${padding}%` }}>
    <Image className={`position-absolute  h-100 w-100 object-center-${isCover ? 'cover' : 'contain'}`} loading="lazy" src={url} height={height} width={width} data-load="0"  data-id={item?.id}  data-network={item?.networkId} alt={alt != '' ? alt : GET_MEDIA_NAME(url)} onError={handleError} fluid />
  </div>
}
export default ImageType