import { useEffect, useState } from 'react';
import ReactPlayer from 'react-player';
import { getImageWidthHeight, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO } from '../../../../../utils';

const VideoType = ({ item, mediaClass, size, height, width, url, poster, autoPlay, muted, isCover, controls, loop ,isupdate}) => {

  const [videoLoaded, setVideoLoaded] = useState(false)
  const [padding, setPadding] = useState(size ? size : 100)

  const onMediaLoad = async () => {
    if(size){
      setPadding(size)
    }else{
      if(height && height != "" && width && width != ""){
        setPadding((height * 100) / width)
      }else{
        try {
          const { width, height } = await getImageWidthHeight(poster);
          
            setPadding((height * 100) / width)
        } catch (error) {
            setPadding(100)
        }
      }
    }
  }


  useEffect(() => {
    onMediaLoad()
  }, [size])

  // const onErrorImageUpdate = event => {
  //   event.target.src = ImagePlaceholder
  // }
  const videoLoadedFun = () => {
    setVideoLoaded(true)
  }
  return <div className={`${mediaClass} overflow-hidden position-relative`} style={{paddingBottom:`${padding}%`}}>
    <div className="position-absolute object-center-cover h-100 w-100">
      <ReactPlayer
        poster={poster}
        className={''}
        url={url}
        config={{
          facebook: { appId: '117029975354072' },
          file: {
            attributes: { 'data-item-id': item?.id, 'poster': poster, 'data-network': item?.networkId,  'data-item-id': item?.id, 'data-stories': item?.stories, "data-load": '0', }
          }
        }}
        autoPlay={autoPlay}
        muted={true}
        volume={autoPlay ? 1 : 0}
        playsinline={true}
        onReady={(e) => { videoLoadedFun(true) }}
        playing={autoPlay}
        height={'100%'}
        width={'100%'}
        loop={loop}
        controls={controls}
        onError={isupdate ?  (e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e)}: ()=>{}}
        style={{ backgroundImage: (!videoLoaded) ? `url(${poster})` : '', backgroundSize: isCover ? 'cover' : 'contain', backgroundPosition: 'center', backgroundRepeat: 'no-repeat' }}
      /> 
    </div>
  </div>
}
export default VideoType