import React, { Component } from 'react';
import { Container, Card, Table, Tooltip, OverlayTrigger, Button, Form, Image, Row, Col } from 'react-bootstrap'
import { FontAwesomeIcon, baseUrl } from '../../../../../Assets/AssetsHelper';
import { Link } from 'react-router-dom';
import Author from '../../CommonComponents/Author/Author';
import ImageType from '../../CommonComponents/MediaType/ImageType';
import { GET_NETWORK_ICON, GET_NETWORK_NAME, TOAST_ERROR, TOAST_SUCCESS } from '../../../../../utils';
import ShowMoreContent from '../../CommonComponents/ShowMoreContent';
import { Loader } from '../../CommonComponents/Loader';
import ManualUploadModal from './ManualUploadModal';
import { connect } from 'react-redux';
import { customPostsubmit, deleteCustomPost, geteditPost, getPost, statusUpdateCustomPost } from '../../../../../actions/appsMoreAction';
import moment from 'moment';
import EmptyWithIcon from '../../CommonComponents/Empty/EmptyWithIcon';
import "./manual-upload.scss"

const ActionButton = ({ toolTip, clickAction, post, iconImg }) => {
       return <OverlayTrigger placement="top" overlay={<Tooltip>{toolTip}</Tooltip>}>
              <Button id={`action-${iconImg}`} variant="icon" className="btn-active-primary ms-2" onClick={event => clickAction(post.id)}>
                     <FontAwesomeIcon type="solid" icon={iconImg} />
              </Button>
       </OverlayTrigger>
}
const ItemCell = (props) => <th className="fw-bold text-nowrap" style={{ minWidth: props.width }}>{props.children}</th>
class ManualUpload extends Component {
       state = {
              postList: [],
              loader: true,
              showModal: false,
              spinner: false,
              content: '',
              isEdit: false,
              editData: {},
              contentError: false
       }

       componentDidMount() {
              this.getPostData()
       }

       getPostData = () => {
              getPost(localStorage.getItem('wallId')).then(response => {
                     const { responseData } = response.data
                     this.setState({ postList: responseData.customPosts, loader: false })
              })
       }

       editPost = (id) => {
              this.setState({ isEdit: true, showModal: true })
              geteditPost(id).then(response => {
                     const { responseData } = response.data
                     this.setState({ editData: responseData[0], content: responseData[0].content })
              })
       }

       changeStatus = (post) => {
              statusUpdateCustomPost(localStorage.getItem('wallId'), post.id, post.status == 1 ? 0 : 1).then(response => {
                     this.getPostData()
                     TOAST_SUCCESS("Status Updated Successfully.")
              })
       }

       deletePost = (id) => {
              deleteCustomPost(localStorage.getItem('wallId'), id).then(res => {
                     this.getPostData()
                     TOAST_SUCCESS("Post Deleted Successfully.")
              })
       }

       changeContent = (event) => this.setState({ content: event.target.value })

       uploadPost = (data) => {

              const { userData } = this.props
              const { content, isEdit, editData } = this.state
              const formData = new FormData();

              if (isEdit) {
                     formData.append('eid', editData.postEditId);
                     formData.append('action', "update");
                     // formData.append('link', editData.link);
                     // formData.append('feedId', editData.feedId);
              }

              else {
                     // formData.append('createdAt', moment(moment(new Date())).format('YYYY-MM-DD HH:mm'));
                     // formData.append('link', '');
                     // formData.append('feedId', 0);
              }

              let type = 1;
              this.setState({ spinner: true });
              if (data.mediaType == 'content' || data.mediaType == 1) {
                     type = 1;
              } else if ((data.mediaType == 'image' || data.mediaType == 4) && content) {
                     type = 4;
              } else if ((data.mediaType == 'image' || data.mediaType == 2) && !content) {
                     type = 2;
              } else if ((data.mediaType == 'video' || data.mediaType == 3) && !content) {
                     type = 3;
              } else if ((data.mediaType == 'video' || data.mediaType == 5) && content) {
                     type = 5;
              }

              formData.append('postFile', data.mediaType == 'video' ? data.thumbFile : data.postImage);
              formData.append('mediaFile', data.mediaType == 'video' ? data.postImage : "");
              // formData.append('mediaFilePath', data.mediaType == 'video' ? data.postImage : "");

              // formData.append('authorFile', userData.picture);
              // formData.append('authorName', userData.name);
              formData.append('content', this.state.content);
              // formData.append('networkName', 'No Network');
              // formData.append('networkId', 0);
              // formData.append('recureEveryOption', 0);

              // formData.append('recureEvery', 0);
              // formData.append('recureAfter', 0);
              // formData.append('ownerId', userData.ownerId);
              // formData.append('referenceId', parseInt(Date.now()));
              formData.append('mediaHeight', data.height ? data.height : 0);
              formData.append('mediaWidth', data.width ? data.width : 0);
              formData.append('type', type);
              formData.append('wallId', localStorage.getItem('wallId'));
              // formData.append('postId', 0);
              // formData.append('rating', 0);
              // formData.append('isAdmin', 0);
              // formData.append('adminId', 0);
              // formData.append('videoType', 0);

              if (String(content).trim().length > 0) {
                     customPostsubmit(userData.id, localStorage.getItem('wallId'), formData).then((response) => {
                            if (response.data.responseCode == 200) {
                                   if (response.data.responseStatus == 'success') {
                                          // ${values.eid ? 'update' : 'created'}
                                          TOAST_SUCCESS(`Post  successfully.`);
                                          this.setState({ loader: true, showModal: false, contentError: false })
                                          this.getPostData()
                                   } else {
                                          TOAST_ERROR('Error');
                                   }
                            } else if (response.data.responseCode == 409) {
                                   TOAST_ERROR("Post Already Exists.");
                            }
                            this.setState({ spinner: false });
                     }).catch((error) => {
                            TOAST_ERROR(error.message);
                            this.setState({ spinner: false })

                     })
              }
              else this.setState({ spinner: false, contentError: true })

       }

       render() {
              const { loader, postList, showModal, spinner, isEdit, editData, content, contentError } = this.state
              return (
                     <div className='d-flex flex-column flex-column-fluid w-100 py-5 manual_upload_'>
                            <Container fluid>
                                   <Card className='border-none'>
                                          {/* <Card.Header className='f-between p-5'>
                                                 <div className="d-flex flex-wrap align-items-center">
                                                        <div className="symbol symbol-44 flex-shrink-0 me-4">
                                                               <Image src={baseUrl(`media/icons/social-icons-square/manual-upload.svg`)} fluid />
                                                        </div>
                                                        <div className="d-flex flex-column flex-grow-1 my-lg-0 my-2 pe-3">
                                                               <h3 className="mb-0 h4">Manual Upload</h3>
                                                               <p className="text-muted mb-0 fw-medium fs-8">Manual Upload List</p>
                                                        </div>
                                                 </div>
                                                 <div className='d-flex flex-shrink-0'>
                                                        <Button id="add_pro" variant='primary' size="sm" className="mb-2 text-nowrap" disabled={loader} onClick={() => this.setState({ showModal: true })}>
                                                               <FontAwesomeIcon icon={'plus'} />
                                                               Create New Post
                                                        </Button>
                                                 </div>
                                          </Card.Header> */}
                                          <Card.Body className='f-center flex-column' style={{minHeight:'calc(100vh - 100px)'}}>

                                                 <h3 className='text-center mb-3'>Manual Uploads</h3>
                                                 <p className='text-center mb-8'>Lorem Ipsum is simply dummy text of the printing and typesetting industry. </p>
                                                 <div className='f-center flex-wrap w-100'>
                                                        <Row className='w-100' style={{maxWidth:600}}>
                                                               <Col>
                                                                      <Link to={`/manual-upload/feed`} className='btn btn-outline-secondary w-100 rounded-1 f-center flex-column p-7 mb-4'>
                                                                             <p className='display-2 mb-3'>
                                                                                    <FontAwesomeIcon type={'light'} icon={'laptop'} />
                                                                             </p>
                                                                             <p className='mb-0 fs-10 fw-medium'>My Device</p>
                                                                      </Link>
                                                               </Col>
                                                               <Col>
                                                                      <Link to={`/manual-upload/feed`} className='btn btn-outline-secondary w-100 rounded-1 f-center flex-column p-7 mb-4'>
                                                                             <p className='display-2 mb-3'>
                                                                                    <Image src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(2)}.svg`)} height={36} width={36} fluid/>
                                                                             </p>
                                                                             <p className='mb-0 fs-10 fw-medium'>{GET_NETWORK_NAME(2)}</p>
                                                                      </Link>
                                                               </Col>
                                                               <Col>
                                                                      <Link to={`/manual-upload/feed`} className='btn btn-outline-secondary w-100 rounded-1 f-center flex-column p-7 mb-4'>
                                                                             <p className='display-2 mb-3'>
                                                                                    <Image src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(28)}.svg`)} height={36} width={36} fluid/>
                                                                             </p>
                                                                             <p className='mb-0 fs-10 fw-medium'>{GET_NETWORK_NAME(28)}</p>
                                                                      </Link>
                                                               </Col>
                                                        </Row>
                                                 </div>
                                          </Card.Body>
                                   </Card>
                            </Container>
                            {showModal ? <ManualUploadModal closeModal={() => this.setState({ showModal: false, isEdit: false, editData: {}, content: '', contentError: false })} spinner={spinner} uploadPost={this.uploadPost} isEdit={isEdit} editData={editData} changeContent={this.changeContent} content={content} contentError={contentError} /> : null}
                     </div>
              );
       }
}

const mapStateToProps = (state) => {
       return {
              userData: state.authorized?.userData?.user_data?.user,
       }
}

export default connect(mapStateToProps, null)(ManualUpload);